package com.kalingas.pos.dev.datasource.cache

import app.cash.sqldelight.SuspendingTransacter
import app.cash.sqldelight.db.QueryResult
import app.cash.sqldelight.db.SqlDriver
import app.cash.sqldelight.db.SqlSchema
import com.kalingas.pos.dev.datasource.cache.composeApp.newInstance
import com.kalingas.pos.dev.datasource.cache.composeApp.schema
import kotlin.Unit

public interface KalingasDatabase : SuspendingTransacter {
  public val kalingasDatabaseQueries: KalingasDatabaseQueries

  public companion object {
    public val Schema: SqlSchema<QueryResult.AsyncValue<Unit>>
      get() = KalingasDatabase::class.schema

    public operator fun invoke(driver: SqlDriver): KalingasDatabase =
        KalingasDatabase::class.newInstance(driver)
  }
}
