package com.kalingas.pos.dev.presentation.splash.view

import androidx.compose.foundation.background
import androidx.compose.foundation.layout.Arrangement
import androidx.compose.foundation.layout.Column
import androidx.compose.foundation.layout.Spacer
import androidx.compose.foundation.layout.fillMaxSize
import androidx.compose.foundation.layout.height
import androidx.compose.runtime.Composable
import androidx.compose.runtime.LaunchedEffect
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import androidx.compose.ui.platform.LocalUriHandler
import androidx.compose.ui.unit.dp
import com.kalingas.pos.dev.interactor.event.UiEvents
import com.kalingas.pos.dev.interactor.uistate.UiState
import com.kalingas.pos.dev.presentation.common.image.SplashImageCircularAnimated
import com.kalingas.pos.dev.presentation.common.text.Text14RegularBB
import com.kalingas.pos.dev.presentation.common.text.Text24SemiBold
import com.kalingas.pos.dev.presentation.splash.event.SplashUiEvents
import com.kalingas.pos.dev.theme.splashBack
import com.kalingas.pos.dev.theme.white
import com.kalingas.pos.dev.util.AppInfo

@Suppress("UNUSED_PARAMETER")
@Composable
fun SplashView(uiState: UiState, onTriggerEvent: (UiEvents) -> Unit) {
    val uriHandler = LocalUriHandler.current
    Column(
        modifier = Modifier
            .fillMaxSize()
            .background(color = splashBack),
        verticalArrangement = Arrangement.Center,
        horizontalAlignment = Alignment.CenterHorizontally
    ) {
        Spacer(modifier = Modifier.weight(1f))
        SplashImageCircularAnimated()
        Spacer(modifier = Modifier.height(16.dp))
        Text24SemiBold(text = "Kalingas POS", color = white)
        Spacer(modifier = Modifier.weight(1f))
        Text14RegularBB(text = "Version: ${AppInfo.versionName}", color = white)
        Spacer(modifier = Modifier.height(24.dp))
    }

    LaunchedEffect(Unit) {
        onTriggerEvent(UiEvents.SplashEvents(SplashUiEvents.GetSplashUiData(uriHandler)))
    }
}
