package com.kalingas.pos.dev.presentation.navigation.manager

import com.kalingas.pos.dev.interactor.manager.StateManager
import com.kalingas.pos.dev.presentation.navigation.event.NavigationUiEvents

object NavigationManager {
    fun onTriggerEvent(event: NavigationUiEvents) {
        when (event) {
            is NavigationUiEvents.Show -> StateManager.updateState(
                StateManager.getState().copy(navView = event.screen, mainView = event.screen)
            )
        }
    }
}
