package com.kalingas.pos.dev.presentation.splash.manager

import com.kalingas.pos.dev.interactor.manager.StateManager
import com.kalingas.pos.dev.presentation.Screens
import com.kalingas.pos.dev.presentation.splash.event.SplashUiEvents
import kotlinx.coroutines.delay

object SplashManager {
    fun onTriggerEvent(event: SplashUiEvents) {
        when (event) {
            is SplashUiEvents.GetSplashUiData -> preloadApp()
        }
    }

    private fun preloadApp() {
        StateManager.launchDefault {
            StateManager.updateState(StateManager.getState().copy(isLoading = true))
            delay(1800)
            goToLogin()
        }
    }

    private fun goToLogin() {
        val uiState = StateManager.getState()
        StateManager.updateState(
            uiState.copy(
                mainView = Screens.Login,
                navView = null,
                selectedRole = null,
                loginPassword = "",
                loginError = null,
                isLoading = false,
            )
        )
    }
}
