package com.kalingas.pos.dev.presentation.order.viewmodel

import com.kalingas.pos.dev.interactor.uistate.FoodItem
import kotlin.math.round
import kotlin.random.Random
import kotlin.uuid.ExperimentalUuidApi
import kotlin.uuid.Uuid

enum class OrderType { STORE, ONLINE }
enum class StoreMode { DINE_IN, PARCEL }
enum class OnlinePlatform { SWIGGY, ZOMATO, OTHER }

data class OrderTotals(
    val subtotalBeforeTax: Double = 0.0,
    val sgst: Double = 0.0,
    val cgst: Double = 0.0,
    val total: Double = 0.0,
    val parcel: Boolean = false
)

data class OrderDraft(
    val orderId: Long,
    val tokenNumber: Long,
    val orderType: OrderType,
    val parcel: Boolean,
    val totals: OrderTotals = OrderTotals()
) {
    companion object {
        fun buildDraft(orderType: OrderType): OrderDraft = OrderDraft(
            orderId = Random.nextLong(1, Long.MAX_VALUE),
            tokenNumber = Random.nextLong(1, 10_000),
            orderType = orderType,
            parcel = orderType == OrderType.ONLINE
        )
    }
}

@OptIn(ExperimentalUuidApi::class)
data class OrderLineDraft(
    val lineId: String = Uuid.random().toString(),
    val itemId: String,
    val itemName: String,
    val variantName: String?,
    val quantity: Double,
    val unitLabel: String,
    val unitPrice: Double,
    val gstRate: Int,
    val source: String,
    val gross: Double,
    val preTax: Double,
    val gstPortion: Double
) {
    companion object {
        fun fromItem(item: FoodItem, orderType: OrderType, quantity: Double): OrderLineDraft {
            val unitPrice = if (orderType == OrderType.STORE) item.storePrice else item.onlinePrice
            val portionSize = if (item.unitLabel.equals("ml", ignoreCase = true) && item.quantityStep >= 100.0) item.quantityStep else null
            val effectiveQuantity = portionSize?.let { quantity / it } ?: quantity
            val gross = roundTwo(unitPrice * effectiveQuantity)
            val gstPortion = if (item.gstRate == 0) 0.0 else gross - gross / (1 + item.gstRate / 100.0)
            val preTax = gross - gstPortion
            return OrderLineDraft(
                itemId = item.id,
                itemName = item.name,
                variantName = item.variantName,
                quantity = roundTwo(quantity),
                unitLabel = item.unitLabel,
                unitPrice = unitPrice,
                gstRate = item.gstRate,
                source = item.source,
                gross = roundTwo(gross),
                preTax = roundTwo(preTax),
                gstPortion = roundTwo(gstPortion)
            )
        }
    }
}

fun calculateTotals(
    orderLines: List<OrderLineDraft>,
    orderType: OrderType,
    parcelCharge: Double,
    isStoreParcel: Boolean = false
): OrderTotals {
    val subtotal = roundTwo(orderLines.sumOf { it.preTax })
    val gst = roundTwo(orderLines.sumOf { it.gstPortion })
    val halfGst = roundTwo(gst / 2)
    val parcel = when (orderType) {
        OrderType.ONLINE -> 0.0
        OrderType.STORE -> if (isStoreParcel) roundTwo(parcelCharge) else 0.0
    }
    val total = roundTwo(subtotal + halfGst * 2 + parcel)
    return OrderTotals(
        subtotalBeforeTax = subtotal,
        sgst = halfGst,
        cgst = halfGst,
        total = total,
        parcel = parcel > 0.0
    )
}

fun roundTwo(value: Double): Double = round(value * 100) / 100.0
