package com.kalingas.pos.dev.mqtt

import com.kalingas.pos.dev.datasource.mqtt.MQTTEvents
import com.kalingas.pos.dev.datasource.mqtt.MQTTManager
import com.kalingas.pos.dev.datasource.mqtt.mqttCaCertificatePem
import com.kalingas.pos.dev.util.getMQTTPassword
import com.kalingas.pos.dev.util.getMQTTUrl
import com.kalingas.pos.dev.util.getMQTTUserName
import com.kalingas.pos.dev.util.getMQTTWebPort

external fun createMQTTClient()
external fun connect()
external fun disconnect()
external fun publish(topic: String, message: String)
external fun subscribe(topic: String)
external fun unsubscribe(topic: String)

@JsExport
fun connectionLost(message: String?) {
    MQTTManager.onTriggerMQTTEvent(MQTTEvents.OnDisconnected(message ?: "Unknown Error"))
}

@JsExport
fun messageArrived(topic: String?, message: String?) {
    MQTTManager.onTriggerMQTTEvent(
        MQTTEvents.OnMessageReceived(topic.orEmpty(), message.orEmpty())
    )
}

@JsExport
fun connectComplete() {
    MQTTManager.onTriggerMQTTEvent(MQTTEvents.OnConnected)
}

@JsExport
fun getMqttUrl(): String = getMQTTUrl()

@JsExport
fun getMqttPort(): Int = getMQTTWebPort().toInt()

@JsExport
fun getMqttUserName(): String = getMQTTUserName()

@JsExport
fun getMqttPassword(): String = getMQTTPassword()

@JsExport
fun getMqttCertificatePem(): String = mqttCaCertificatePem()
