package com.kalingas.pos.dev.interactor.uistate

import kotlin.math.max

object MenuCatalog {
    fun menuCatalog(): List<FoodCategory> = buildMenuCatalog()
}

private data class CategoryAccumulator(
    val id: Int,
    val name: String,
    val items: MutableList<FoodItem> = mutableListOf()
)

private data class VariantSpec(
    val baseName: String,
    val variantName: String?,
    val storePrice: Double,
    val onlinePrice: Double,
    val unit: ItemUnit,
    val unitLabel: String,
    val quantityStep: Double,
    val minStoreQty: Double,
    val minOnlineQty: Double,
    val storeDisplayText: String,
    val onlineDisplayText: String,
    val source: String,
    val gstRate: Int,
)

private data class PriceDetails(
    val amount: Double,
    val unit: ItemUnit?,
    val unitLabel: String?,
    val quantityStep: Double?,
    val minQuantity: Double?,
    val displayText: String,
)

private data class UnitMeta(
    val unit: ItemUnit,
    val unitLabel: String,
    val quantityStep: Double,
    val minQuantity: Double,
)

private fun buildMenuCatalog(): List<FoodCategory> {
    val categories = mutableListOf<CategoryAccumulator>()
    var currentCategory: CategoryAccumulator? = null
    RAW_MENU_DATA.lineSequence().forEach { rawLine ->
        if (rawLine.isBlank()) return@forEach
        val columns = rawLine.split('\t').map { it.trim() }
        val first = columns.getOrNull(0)?.trim().orEmpty()
        if (first.isBlank()) return@forEach

        val storeCol = columns.getOrNull(1)?.trim().orEmpty()
        val onlineCol = columns.getOrNull(2)?.trim().orEmpty()
        val looksLikeHeader = storeCol.equals("Inhouse", true) && onlineCol.equals("Online", true)
        val isCategory = looksLikeHeader || (storeCol.isBlank() && onlineCol.isBlank())

        if (isCategory) {
            val name = normalizeSpaces(first)
            if (name.isNotEmpty()) {
                val category = CategoryAccumulator(categories.size + 1, name)
                categories += category
                currentCategory = category
            }
        } else {
            val category = currentCategory ?: return@forEach
            val specs = expandVariants(category.name, first, storeCol, onlineCol)
            if (specs.isEmpty()) {
                val fallbackSpec = VariantSpec(
                    baseName = normalizeSpaces(first),
                    variantName = null,
                    storePrice = 0.0,
                    onlinePrice = 0.0,
                    unit = ItemUnit.UNIT,
                    unitLabel = "unit",
                    quantityStep = 1.0,
                    minStoreQty = 1.0,
                    minOnlineQty = 1.0,
                    storeDisplayText = storeCol,
                    onlineDisplayText = onlineCol,
                    source = determineSource(category.name),
                    gstRate = 5
                )
                category.items += fallbackSpec.toFoodItem(category.id, category.items.size)
            } else {
                specs.forEach { spec ->
                    category.items += spec.toFoodItem(category.id, category.items.size)
                }
            }
        }
    }

    return categories.map { accumulator ->
        FoodCategory(
            id = accumulator.id,
            name = accumulator.name,
            items = accumulator.items.toList(),
            isExpanded = accumulator.id == 1,
            isActive = true
        )
    }
}

private fun VariantSpec.toFoodItem(categoryId: Int, index: Int): FoodItem {
    val itemId = "$categoryId-${index + 1}"
    return FoodItem(
        id = itemId,
        name = baseName,
        storePrice = storePrice,
        onlinePrice = onlinePrice,
        gstRate = gstRate,
        source = source,
        categoryId = categoryId,
        isActive = true,
        unit = unit,
        unitLabel = unitLabel,
        quantityStep = quantityStep,
        minStoreQuantity = minStoreQty,
        minOnlineQuantity = minOnlineQty,
        variantName = variantName,
        storePriceText = storeDisplayText,
        onlinePriceText = onlineDisplayText
    )
}

private fun expandVariants(
    categoryName: String,
    rawName: String,
    storeColumn: String,
    onlineColumn: String
): List<VariantSpec> {
    val (baseName, variantLabels) = extractVariantLabels(rawName)
    val storeTokens = splitPriceTokens(storeColumn).ifEmpty { listOf(storeColumn) }
    val onlineTokens = splitPriceTokens(onlineColumn).ifEmpty {
        if (onlineColumn.isBlank()) storeTokens else listOf(onlineColumn)
    }

    val maxCount = max(1, max(variantLabels.ifEmpty { listOf("") }.size, max(storeTokens.size, onlineTokens.size)))
    val specs = mutableListOf<VariantSpec>()
    val unitHint = inferUnitMeta("$rawName $storeColumn")
        ?: inferUnitMeta("$rawName $onlineColumn")

    repeat(maxCount) { index ->
        val variantName = variantLabels.getOrNull(index)
        val storeToken = storeTokens.getOrNull(index) ?: storeTokens.lastOrNull().orEmpty()
        val onlineToken = onlineTokens.getOrNull(index) ?: onlineTokens.lastOrNull().orEmpty()
        val storeDetails = parsePriceDetails(storeToken, baseName, unitHint)
        val onlineDetails = parsePriceDetails(onlineToken, baseName, unitHint)

        val unit = storeDetails.unit ?: onlineDetails.unit ?: unitHint?.unit ?: ItemUnit.UNIT
        val unitLabel = storeDetails.unitLabel ?: onlineDetails.unitLabel ?: unitHint?.unitLabel ?: "unit"
        val quantityStep = storeDetails.quantityStep ?: onlineDetails.quantityStep ?: unitHint?.quantityStep ?: 1.0
        val minStoreQty = storeDetails.minQuantity ?: unitHint?.minQuantity ?: storeDetails.defaultMinQuantity()
        val minOnlineQty = onlineDetails.minQuantity
            ?: unitHint?.minQuantity
            ?: storeDetails.minQuantity
            ?: onlineDetails.defaultMinQuantity()

        val spec = VariantSpec(
            baseName = baseName,
            variantName = variantName,
            storePrice = storeDetails.amount,
            onlinePrice = onlineDetails.amount,
            unit = unit,
            unitLabel = unitLabel,
            quantityStep = quantityStep,
            minStoreQty = minStoreQty,
            minOnlineQty = minOnlineQty,
            storeDisplayText = storeDetails.displayText,
            onlineDisplayText = onlineDetails.displayText,
            source = determineSource(categoryName),
            gstRate = 5
        )
        specs += adjustPortionSpec(rawName, spec)
    }
    return specs
}

private fun adjustPortionSpec(rawName: String, spec: VariantSpec): VariantSpec {
    if (!rawName.contains("(100ml/300ml)", ignoreCase = true)) return spec
    val variantLabel = spec.variantName?.lowercase() ?: return spec
    val portion = when {
        variantLabel.contains("300") -> 300.0
        variantLabel.contains("100") -> 100.0
        else -> return spec
    }
    return spec.copy(
        unit = ItemUnit.VOLUME,
        unitLabel = "ml",
        quantityStep = portion,
        minStoreQty = portion,
        minOnlineQty = portion
    )
}

private fun parsePriceDetails(token: String, contextName: String, hint: UnitMeta?): PriceDetails {
    val normalized = token.trim()
    val numberMatches = NUMBER_REGEX.findAll(normalized).toList()
    val amount = numberMatches.firstOrNull()?.value?.toDoubleOrNull() ?: 0.0

    var minQuantity = if (normalized.contains("for", ignoreCase = true) && numberMatches.size >= 2) {
        numberMatches[1].value.toDoubleOrNull()
    } else null

    val combinedContext = "$normalized $contextName"
    val unitMeta = inferUnitMeta(combinedContext) ?: hint
    val unit = unitMeta?.unit ?: guessUnit(normalized)

    if (minQuantity != null && normalized.contains("ml", ignoreCase = true)) {
        minQuantity/= 1000.0
    } else if (minQuantity != null && normalized.contains("gm", ignoreCase = true)) {
        minQuantity/= 1000.0
    }

    val unitLabel = unitMeta?.unitLabel ?: guessUnitLabel(normalized, unit)
    val quantityStep = unitMeta?.quantityStep ?: defaultStep(unit, minQuantity)
    val adjustedMinQuantity = minQuantity ?: unitMeta?.minQuantity

    return PriceDetails(
        amount = amount,
        unit = unit,
        unitLabel = unitLabel,
        quantityStep = quantityStep,
        minQuantity = adjustedMinQuantity,
        displayText = normalized
    )
}

private fun PriceDetails.defaultMinQuantity(): Double = when (unit) {
    ItemUnit.WEIGHT -> 0.1
    ItemUnit.VOLUME -> 0.1
    else -> 1.0
}

private fun defaultStep(unit: ItemUnit, minQuantity: Double?): Double = when (unit) {
    ItemUnit.WEIGHT -> minQuantity ?: 0.1
    ItemUnit.VOLUME -> minQuantity ?: 0.1
    else -> 1.0
}

private fun determineSource(categoryName: String): String = when {
    categoryName.contains("Snacks", true) -> "COUNTER"
    categoryName.contains("Breads", true) -> "COUNTER"
    categoryName.contains("Desserts", true) -> "KITCHEN"
    categoryName.contains("Beverages", true) -> "COUNTER"
    else -> "KITCHEN"
}

private fun extractVariantLabels(rawName: String): Pair<String, List<String>> {
    var cleanedName = rawName
    var labels: List<String> = emptyList()
    VARIANT_REGEX.findAll(rawName).forEach { match ->
        val content = match.groupValues[1]
        if (content.contains("/") || content.contains("|")) {
            labels = content.split('/', '|')
                .map { normalizeSpaces(it.replace("pcs", "", true)) }
                .filter { it.isNotBlank() }
            cleanedName = cleanedName.replace(match.value, "").trim()
        }
    }
    cleanedName = normalizeSpaces(cleanedName)
    return cleanedName to labels
}

private fun splitPriceTokens(raw: String): List<String> {
    if (raw.isBlank()) return emptyList()
    val trimmed = raw.trim()
    listOf("/", "|").forEach { delimiter ->
        if (trimmed.contains(delimiter)) {
            val tokens = trimmed.split(delimiter).map { it.trim() }
            if (tokens.size > 1 && tokens.all { NUMBER_REGEX.containsMatchIn(it) }) {
                return tokens
            }
        }
    }
    return listOf(trimmed)
}

private fun inferUnitMeta(text: String): UnitMeta? {
    val lowered = text.lowercase()
    return when {
        lowered.contains("kg") -> UnitMeta(ItemUnit.WEIGHT, "kg", 0.1, 0.1)
        lowered.contains("gms") || lowered.contains("gm") -> {
            val grams = Regex("""(\d+)\s*gms?""").find(lowered)?.groupValues?.get(1)?.toDoubleOrNull()
            val kg = grams?.div(1000.0)
            if (kg != null) UnitMeta(ItemUnit.WEIGHT, "kg", kg, kg) else UnitMeta(ItemUnit.WEIGHT, "kg", 0.1, 0.1)
        }
        lowered.contains("ml") || lowered.contains("ltr") -> {
            val ml = Regex("""(\d+)\s*ml""").find(lowered)?.groupValues?.get(1)?.toDoubleOrNull()
            val liters = ml?.div(1000.0)
            if (liters != null) UnitMeta(ItemUnit.VOLUME, "ltr", liters, liters) else UnitMeta(ItemUnit.VOLUME, "ltr", 0.1, 0.1)
        }
        else -> null
    }
}

private fun guessUnit(text: String): ItemUnit = when {
    text.contains("kg", true) || text.contains("gm", true) -> ItemUnit.WEIGHT
    text.contains("ml", true) || text.contains("ltr", true) -> ItemUnit.VOLUME
    else -> ItemUnit.UNIT
}

private fun guessUnitLabel(text: String, unit: ItemUnit): String = when (unit) {
    ItemUnit.WEIGHT -> "kg"
    ItemUnit.VOLUME -> if (text.contains("ml", true)) "ml" else "ltr"
    else -> "unit"
}

private fun normalizeSpaces(input: String): String = input.replace(Regex("\\s+"), " ").trim()

private val NUMBER_REGEX = Regex("""\d+(\.\d+)?""")
private val VARIANT_REGEX = Regex("""\(([^()]*(?:/|\|)[^()]*)\)""")

