package com.kalingas.pos.dev.datasource.mqtt

import com.kalingas.pos.dev.util.getMQTTCertificate

private val MQTT_CA_CERTIFICATE_PEM: String = getMQTTCertificate()

fun mqttCaCertificatePem(): String = MQTT_CA_CERTIFICATE_PEM

fun mqttCaCertificateBytes(): ByteArray =
    if (MQTT_CA_CERTIFICATE_PEM.isBlank()) ByteArray(0) else MQTT_CA_CERTIFICATE_PEM.encodeToByteArray()
