import com.kalingas.pos.dev.presentation.order.event.OrderUiEvents

private enum class TestLabTab(val label: String) {
    OWNER("Owner"),
    HUB("Hub"),
    SWEET("Sweet Counter"),
    MAIN("Main Course"),
    KITCHEN("Kitchen"),
    CASH("Cash Counter")
}

@Composable
fun PosTestLabView(uiState: UiState, onTriggerEvent: (UiEvents) -> Unit) {
    var selectedTab by remember { mutableStateOf(TestLabTab.OWNER) }
    Column(modifier = Modifier.fillMaxSize().padding(16.dp)) {
        Text("POS Test Lab", style = MaterialTheme.typography.headlineSmall, color = MaterialTheme.colorScheme.primary)
        Text(
            "Switch between roles to simulate the full workflow. Actions here reuse the same data as the individual logins.",
            style = MaterialTheme.typography.bodySmall,
            color = MaterialTheme.colorScheme.onSurfaceVariant,
            modifier = Modifier.padding(top = 4.dp, bottom = 12.dp)
        )
        TabRow(selectedTabIndex = selectedTab.ordinal) {
            TestLabTab.values().forEach { tab ->
                Tab(selected = tab == selectedTab, onClick = { selectedTab = tab }, text = { Text(tab.label) })
            }
        }
        Spacer(modifier = Modifier.height(16.dp))
        when (selectedTab) {
            TestLabTab.OWNER -> OwnerOrdersBoard(uiState = uiState, onLogout = {})
            TestLabTab.HUB -> HubBoard()
            TestLabTab.SWEET -> CounterOrdersBoard(
                title = "Sweet & Snack Counter",
                description = "Add/edit orders and watch other roles react.",
                station = CounterStation.SWEET_SNACK,
                uiState = uiState,
                onEditOrder = { orderId ->
                    onTriggerEvent(UiEvents.OrderEvents(OrderUiEvents.Edit(orderId, Screens.PosTestLab)))
                },
                onAddOrder = {
                    onTriggerEvent(UiEvents.OrderEvents(OrderUiEvents.StartNew(CounterStation.SWEET_SNACK, Screens.PosTestLab)))
                },
                onLogout = {}
            )
            TestLabTab.MAIN -> CounterOrdersBoard(
                title = "Main Course Counter",
                description = "Work on entres in parallel.",
                station = CounterStation.MAIN_COURSE,
                uiState = uiState,
                onEditOrder = { orderId ->
                    onTriggerEvent(UiEvents.OrderEvents(OrderUiEvents.Edit(orderId, Screens.PosTestLab)))
                },
                onAddOrder = {
                    onTriggerEvent(UiEvents.OrderEvents(OrderUiEvents.StartNew(CounterStation.MAIN_COURSE, Screens.PosTestLab)))
                },
                onLogout = {}
            )
            TestLabTab.KITCHEN -> KitchenCounterBoard(uiState = uiState, onLogout = {})
            TestLabTab.CASH -> CashCounterBoard(uiState = uiState, onLogout = {})
        }
    }
}

@Composable
private fun HubBoard() {
    val orders = OrderStore.orders
    val totalGross = orders.sumOf { it.totals().total }
    val openCount = orders.count { it.status != DemoOrderStatus.PAID }
    val kitchenPending = orders.sumOf { order -> order.items.count { it.source == "KITCHEN" && it.kitchenStatus != KitchenLineStatus.FULFILLED } }

    Column(
        modifier = Modifier
            .fillMaxSize()
            .padding(16.dp)
    ) {
        Text("Hub Overview", style = MaterialTheme.typography.titleLarge, color = MaterialTheme.colorScheme.primary)
        Spacer(modifier = Modifier.height(12.dp))
        SummaryChip(label = "Open tokens", value = openCount.toString())
        SummaryChip(label = "Kitchen lines pending", value = kitchenPending.toString())
        SummaryChip(label = "Gross", value = totalGross.formatMoney())
        Spacer(modifier = Modifier.height(24.dp))
        Text(
            "Hub represents the syncing service. Any action in this test lab instantly updates all tabs via the shared storeno extra work needed.",
            style = MaterialTheme.typography.bodyMedium,
            color = MaterialTheme.colorScheme.onSurfaceVariant
        )
        Spacer(modifier = Modifier.height(16.dp))
        Button(onClick = { /* no-op, shared store already keeps tabs in sync */ }) {
            Text("Broadcast updates")
        }
    }
}

@Composable
private fun SummaryChip(label: String, value: String) {
    Box(
        modifier = Modifier
            .padding(vertical = 6.dp)
            .fillMaxWidth(),
        contentAlignment = Alignment.CenterStart
    ) {
        Text("$label: $value", style = MaterialTheme.typography.bodyLarge)
    }
}
