package com.kalingas.pos.dev.presentation.navigation.view

import androidx.compose.foundation.background
import androidx.compose.foundation.layout.Arrangement
import androidx.compose.foundation.layout.Column
import androidx.compose.foundation.layout.fillMaxSize
import androidx.compose.foundation.layout.padding
import androidx.compose.material3.Button
import androidx.compose.material3.MaterialTheme
import androidx.compose.material3.Text
import androidx.compose.runtime.Composable
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import androidx.compose.ui.unit.dp
import com.kalingas.pos.dev.interactor.event.UiEvents
import com.kalingas.pos.dev.interactor.uistate.UiState
import com.kalingas.pos.dev.presentation.Screens
import com.kalingas.pos.dev.presentation.navigation.event.NavigationUiEvents
import com.kalingas.pos.dev.theme.screenBackground

@Composable
fun NavigationView(uiState: UiState, onTriggerEvent: (UiEvents) -> Unit) {
    Column(
        modifier = Modifier
            .fillMaxSize()
            .background(screenBackground)
            .padding(horizontal = 24.dp, vertical = 32.dp),
        verticalArrangement = Arrangement.Center,
        horizontalAlignment = Alignment.CenterHorizontally
    ) {
        Text(
            text = "Welcome to Kalingas POS",
            style = MaterialTheme.typography.headlineSmall,
            color = MaterialTheme.colorScheme.onSurface
        )
        uiState.deepLink?.let {
            Text(
                text = "Deep link: $it",
                style = MaterialTheme.typography.bodyMedium,
                color = MaterialTheme.colorScheme.onSurface,
                modifier = Modifier.padding(top = 16.dp)
            )
        }
        Button(
            modifier = Modifier.padding(top = 32.dp),
            onClick = {
                onTriggerEvent(
                    UiEvents.NavigationEvents(
                        NavigationUiEvents.Show(Screens.Navigation)
                    )
                )
            }
        ) {
            Text("Refresh Navigation")
        }
    }
}
