package com.kalingas.pos.dev.presentation.login.manager

import com.kalingas.pos.dev.interactor.manager.StateManager
import com.kalingas.pos.dev.interactor.uistate.UserRole
import com.kalingas.pos.dev.presentation.Screens
import com.kalingas.pos.dev.presentation.login.event.LoginUiEvents

object LoginManager {

    private data class Credential(val role: UserRole, val password: String)

    private val credentials = listOf(
        Credential(UserRole.ADMIN, "@dmin@123"),
        Credential(UserRole.HUB, "Hub@123"),
        Credential(UserRole.OWNER, "S@tya@123"),
        Credential(UserRole.KITCHEN, "Kitchen@123"),
        Credential(UserRole.SWEET_SNACK_COUNTER, "Sweet@123"),
        Credential(UserRole.MAIN_COURSE_COUNTER, "Main@123"),
        Credential(UserRole.CASH_COUNTER, "Cash@123"),
    )

    fun onTriggerEvent(event: LoginUiEvents) {
        when (event) {
            is LoginUiEvents.RoleSelected -> StateManager.updateState(
                StateManager.getState().copy(selectedRole = event.role, loginError = null, loginPassword = "")
            )
            is LoginUiEvents.PasswordChanged -> {
                StateManager.updateState(StateManager.getState().copy(loginPassword = event.value, loginError = null))
            }
            LoginUiEvents.SubmitCredentials -> handleLogin()
            LoginUiEvents.Logout -> logout()
        }
    }

    private fun handleLogin() {
        val state = StateManager.getState()
        val selectedRole = state.selectedRole
        val password = state.loginPassword
        if (selectedRole == null) {
            StateManager.updateState(state.copy(loginError = "Select a user"))
            return
        }
        val credential = credentials.firstOrNull { it.role == selectedRole && it.password == password }
        if (credential != null) {
            StateManager.updateState(
                state.copy(
                    selectedRole = null,
                    loginPassword = "",
                    loginError = null,
                    currentUserRole = credential.role,
                    mainView = credential.role.landingScreen,
                    navView = credential.role.landingScreen,
                    activeOrderId = null,
                    activeOrderReturn = null
                )
            )
        } else {
            StateManager.updateState(state.copy(loginError = "Invalid credentials", loginPassword = ""))
        }
    }

    private fun logout() {
        val state = StateManager.getState()
        StateManager.updateState(
            state.copy(
                selectedRole = null,
                loginPassword = "",
                loginError = null,
                currentUserRole = null,
                mainView = Screens.Login,
                navView = null,
                activeOrderId = null,
                activeOrderReturn = null
            )
        )
    }
}
