package com.kalingas.pos.dev.interactor.event

import com.kalingas.pos.dev.presentation.admin.event.AdminUiEvents
import com.kalingas.pos.dev.presentation.login.event.LoginUiEvents
import com.kalingas.pos.dev.presentation.navigation.event.NavigationUiEvents
import com.kalingas.pos.dev.presentation.order.event.OrderUiEvents
import com.kalingas.pos.dev.presentation.splash.event.SplashUiEvents

sealed class UiEvents {
    data class Portrait(val isPortrait: Boolean) : UiEvents()
    data class SplashEvents(val events: SplashUiEvents) : UiEvents()
    data class NavigationEvents(val events: NavigationUiEvents) : UiEvents()
    data class LoginEvents(val events: LoginUiEvents) : UiEvents()
    data class AdminEvents(val events: AdminUiEvents) : UiEvents()
    data class OrderEvents(val events: OrderUiEvents) : UiEvents()
}
