package com.kalingas.pos.dev.presentation.admin.event

import com.kalingas.pos.dev.interactor.uistate.ItemField
import com.kalingas.pos.dev.interactor.uistate.ItemUnit
import com.kalingas.pos.dev.interactor.uistate.MqttField

sealed class AdminUiEvents {
    data class UpdateMqttField(val field: MqttField, val value: String) : AdminUiEvents()
    data object SaveMqttSettings : AdminUiEvents()

    data class ToggleCategory(val categoryId: Int) : AdminUiEvents()
    data class ToggleItemActive(val categoryId: Int, val itemId: String) : AdminUiEvents()

    data class ShowCategorySheet(val categoryId: Int?) : AdminUiEvents()
    data class ShowItemSheet(val categoryId: Int, val itemId: String?) : AdminUiEvents()
    data object DismissSheet : AdminUiEvents()

    data class UpdateCategoryName(val value: String) : AdminUiEvents()
    data class UpdateCategoryActive(val value: Boolean) : AdminUiEvents()
    data object SaveCategoryForm : AdminUiEvents()

    data class UpdateItemField(val field: ItemField, val value: String) : AdminUiEvents()
    data class UpdateVariantField(val index: Int, val field: ItemField, val value: String) : AdminUiEvents()
    data class UpdateVariantUnit(val index: Int, val unit: ItemUnit) : AdminUiEvents()
    data class UpdateVariantActive(val index: Int, val value: Boolean) : AdminUiEvents()
    data class RemoveVariant(val index: Int) : AdminUiEvents()
    data object AddVariant : AdminUiEvents()
    data object SaveItemForm : AdminUiEvents()
}
