package com.kalingas.pos.dev.presentation.order.manager

import com.kalingas.pos.dev.interactor.manager.StateManager
import com.kalingas.pos.dev.presentation.Screens
import com.kalingas.pos.dev.presentation.dashboard.view.CounterStation
import com.kalingas.pos.dev.presentation.dashboard.view.OrderStore
import com.kalingas.pos.dev.presentation.navigation.event.NavigationUiEvents
import com.kalingas.pos.dev.presentation.navigation.manager.NavigationManager
import com.kalingas.pos.dev.presentation.order.event.OrderUiEvents
import com.kalingas.pos.dev.presentation.order.viewmodel.OrderType

object OrderManager {
    fun onTriggerEvent(event: OrderUiEvents) {
        when (event) {
            is OrderUiEvents.StartNew -> startNewOrder(event.counter, event.returnScreen)
            is OrderUiEvents.Edit -> openExistingOrder(event.orderId, event.returnScreen)
            OrderUiEvents.ClearActive -> clearActiveOrder()
        }
    }

    private fun startNewOrder(counter: CounterStation, returnScreen: Screens?) {
        val order = OrderStore.createOrder(counter, OrderType.STORE)
        setActive(order.id, returnScreen)
        NavigationManager.onTriggerEvent(NavigationUiEvents.Show(counter.toOrderScreen()))
    }

    private fun openExistingOrder(orderId: String, returnScreen: Screens?) {
        val order = OrderStore.findOrder(orderId) ?: return
        setActive(order.id, returnScreen ?: StateManager.getState().activeOrderReturn)
        NavigationManager.onTriggerEvent(NavigationUiEvents.Show(order.counter.toOrderScreen()))
    }

    private fun clearActiveOrder() {
        val state = StateManager.getState()
        if (state.activeOrderId != null || state.activeOrderReturn != null) {
            StateManager.updateState(state.copy(activeOrderId = null, activeOrderReturn = null))
        }
    }

    private fun setActive(orderId: String, returnScreen: Screens?) {
        val state = StateManager.getState()
        if (state.activeOrderId != orderId || state.activeOrderReturn != returnScreen) {
            StateManager.updateState(state.copy(activeOrderId = orderId, activeOrderReturn = returnScreen))
        }
    }
}

private fun CounterStation.toOrderScreen(): Screens = when (this) {
    CounterStation.SWEET_SNACK -> Screens.SweetSnackAddEditOrder
    CounterStation.MAIN_COURSE -> Screens.MainCourseAddEditOrder
}
