package com.kalingas.pos.dev.datasource.cache

import app.cash.sqldelight.async.coroutines.awaitAsOne
import app.cash.sqldelight.async.coroutines.awaitAsOneOrNull
import com.kalingas.pos.dev.interactor.manager.StateManager.launchDefault
import com.kalingas.pos.dev.util.DatabaseDriverFactory
import kotlinx.coroutines.CompletableDeferred
import kotlin.time.Clock

object SqlDelightClient {
    private var database: KalingasDatabase? = null
    private var queries: KalingasDatabaseQueries? = null
    private var initialization: CompletableDeferred<Unit>? = null
    private const val MAX_CACHE_SIZE = 1_000

    fun initDatabase() {
        if (initialization != null) return
        initialization = CompletableDeferred()
        launchDefault {
            database = KalingasDatabase(DatabaseDriverFactory().createDriver())
            queries = database?.kalingasDatabaseQueries
            initialization?.complete(Unit)
        }
    }

    private suspend fun getQueries(): KalingasDatabaseQueries? {
        initialization?.await()
        if (queries == null) {
            queries = database?.kalingasDatabaseQueries
        }
        return queries
    }

    suspend fun putImage(url: String, data: ByteArray) {
        val currentTime = Clock.System.now().epochSeconds
        getQueries()?.updateImageCache(url, data, currentTime)
        val totalItems = getQueries()?.countImagesCache()?.awaitAsOne() ?: 0
        if (totalItems > MAX_CACHE_SIZE) {
            val itemsToEvict = (totalItems - MAX_CACHE_SIZE).coerceAtLeast(0)
            if (itemsToEvict > 0) {
                getQueries()?.evictOldest(itemsToEvict.toLong())
            }
        }
    }

    suspend fun getImage(url: String): ByteArray? {
        val currentTime = Clock.System.now().epochSeconds
        val imageBytes = getQueries()?.getImage(url)?.awaitAsOneOrNull()
        if (imageBytes != null) {
            getQueries()?.updateAccessTime(currentTime, url)
        }
        return imageBytes
    }

    suspend fun clear() {
        getQueries()?.clearImageCache()
    }
}
