package com.kalingas.pos.dev.interactor.viewmodel

import com.kalingas.pos.dev.datasource.cache.SqlDelightClient
import com.kalingas.pos.dev.datasource.mqtt.initializePlatformMqttClient
import com.kalingas.pos.dev.interactor.event.UiEvents
import com.kalingas.pos.dev.interactor.manager.StateManager
import kotlinx.coroutines.CoroutineScope
import kotlinx.coroutines.Dispatchers
import kotlinx.coroutines.SupervisorJob
import kotlinx.coroutines.cancel

class MainViewModel(deepLink: String? = null) {

    val stateManager: StateManager = StateManager

    private val mainDispatcher = runCatching { Dispatchers.Main }.getOrElse { Dispatchers.Default }
    private val scope = CoroutineScope(SupervisorJob() + mainDispatcher)

    init {
        stateManager.setViewModelScope(scope)
        SqlDelightClient.initDatabase()
        initializePlatformMqttClient()
        stateManager.setDeepLink(deepLink)
    }

    fun onTriggerEvent(event: UiEvents) {
        stateManager.onTriggerEvent(event)
    }

    fun clear() {
        scope.cancel()
    }
}
