package com.kalingas.pos.dev.interactor.uistate

import androidx.compose.runtime.mutableStateMapOf
import androidx.compose.runtime.snapshots.SnapshotStateMap
import androidx.compose.ui.graphics.ImageBitmap
import com.kalingas.pos.dev.interactor.util.checkImageCache
import com.kalingas.pos.dev.interactor.util.checkImageCacheCallback
import com.kalingas.pos.dev.presentation.Screens
import com.kalingas.pos.dev.util.getLocalMqttPort
import com.kalingas.pos.dev.util.getLocalMqttUrl
import com.kalingas.pos.dev.util.getLocalMqttWebPort
import com.kalingas.pos.dev.util.getMQTTCertificate
import com.kalingas.pos.dev.util.getMQTTPassword
import com.kalingas.pos.dev.util.getMQTTPort
import com.kalingas.pos.dev.util.getMQTTUrl
import com.kalingas.pos.dev.util.getMQTTUserName
import com.kalingas.pos.dev.util.getMQTTWebPort
import com.kalingas.pos.dev.interactor.uistate.MenuCatalog.menuCatalog

data class UiState(
    val isPortrait: Boolean = true,
    val mainView: Screens = Screens.Splash,
    val navView: Screens? = null,
    val isLoading: Boolean = false,
    val message: String? = null,
    val deepLink: String? = null,
    val imageMap: SnapshotStateMap<String, ImageBitmap> = mutableStateMapOf(),
    val selectedRole: UserRole? = null,
    val mqttFormState: MqttFormState = MqttFormState(),
    val foodCategories: List<FoodCategory> = demoCategories(),
    val adminSheet: AdminSheet? = null,
    val loginPassword: String = "",
    val loginError: String? = null,
    val currentUserRole: UserRole? = null,
    val activeOrderId: String? = null,
    val activeOrderReturn: Screens? = null,
) {
    fun getImage(url: String): ImageBitmap? {
        checkImageCache(url)
        return imageMap[url]
    }

    fun requestImage(url: String, onBitmap: (ImageBitmap?) -> Unit) {
        checkImageCacheCallback(url, onBitmap)
    }
}

enum class UserRole(val displayName: String, val landingScreen: Screens) {
    ADMIN("Admin", Screens.Admin),
    HUB("Hub", Screens.Hub),
    OWNER("Owner", Screens.Owner),
    KITCHEN("Kitchen", Screens.Kitchen),
    SWEET_SNACK_COUNTER("Sweet & Snack Counter", Screens.SweetSnackCounter),
    MAIN_COURSE_COUNTER("Main Course Counter", Screens.MainCourseCounter),
    CASH_COUNTER("Cash Counter", Screens.CashCounter)
}

data class MqttFormState(
    val mqttUrl: String = getMQTTUrl(),
    val mqttPort: String = getMQTTPort(),
    val mqttWebPort: String = getMQTTWebPort(),
    val mqttUsername: String = getMQTTUserName(),
    val mqttPassword: String = getMQTTPassword(),
    val mqttCertificate: String = getMQTTCertificate(),
    val localBrokerUrl: String = getLocalMqttUrl(),
    val localBrokerPort: String = getLocalMqttPort(),
    val localBrokerWebPort: String = getLocalMqttWebPort(),
    val errorFields: Set<MqttField> = emptySet(),
    val savedMessage: String? = null,
)

enum class MqttField {
    Url, Port, WebPort, Username, Password, Certificate, LocalUrl, LocalPort, LocalWebPort
}

data class FoodCategory(
    val id: Int,
    val name: String,
    val items: List<FoodItem>,
    val isExpanded: Boolean = true,
    val isActive: Boolean = true,
)

data class FoodItem(
    val id: String,
    val name: String,
    val storePrice: Double,
    val onlinePrice: Double,
    val gstRate: Int,
    val source: String,
    val categoryId: Int,
    val isActive: Boolean,
    val unit: ItemUnit = ItemUnit.UNIT,
    val unitLabel: String = "unit",
    val quantityStep: Double = 1.0,
    val minStoreQuantity: Double = 1.0,
    val minOnlineQuantity: Double = 1.0,
    val variantName: String? = null,
    val storePriceText: String = "",
    val onlinePriceText: String = "",
)

sealed class AdminSheet {
    data class Category(val form: CategoryFormState) : AdminSheet()
    data class Item(val form: ItemFormState) : AdminSheet()
}

data class CategoryFormState(
    val id: Int? = null,
    val name: String = "",
    val isActive: Boolean = true,
    val nameError: Boolean = false,
)

enum class ItemField {
    Name,
    StorePrice,
    OnlinePrice,
    GstRate,
    Source,
    VariantName,
    UnitLabel,
    QuantityStep,
    MinStoreQuantity,
    MinOnlineQuantity,
}

data class ItemFormState(
    val categoryId: Int,
    val categoryName: String = "",
    val name: String = "",
    val originalName: String? = null,
    val variants: List<VariantFormState> = listOf(VariantFormState()),
    val nameError: Boolean = false,
)

data class VariantFormState(
    val id: String? = null,
    val variantName: String = "",
    val storePrice: String = "",
    val onlinePrice: String = "",
    val gstRate: String = "",
    val source: String = "COUNTER",
    val unit: ItemUnit = ItemUnit.UNIT,
    val unitLabel: String = "unit",
    val quantityStep: String = "",
    val minStoreQuantity: String = "",
    val minOnlineQuantity: String = "",
    val isActive: Boolean = true,
    val storePriceLabel: String = "",
    val onlinePriceLabel: String = "",
    val errors: Set<ItemField> = emptySet(),
)

private fun demoCategories(): List<FoodCategory> = menuCatalog()

enum class ItemUnit {
    UNIT,
    WEIGHT,
    VOLUME,
}
