@OptIn(ExperimentalFoundationApi::class, ExperimentalMaterial3Api::class)
@Composable
fun CounterOrdersBoard(
    title: String,
    description: String,
    station: CounterStation,
    uiState: UiState,
    onEditOrder: ((String) -> Unit)? = null,
    onAddOrder: (() -> Unit)? = null,
    onLogout: () -> Unit
) {
    var searchQuery by remember { mutableStateOf("") }
    var selectedStatus by remember { mutableStateOf<DemoOrderStatus?>(null) }
    val query = searchQuery.trim()
    val orders = OrderStore.orders.filter { order ->
        val matchesQuery = query.isBlank() ||
            order.tokenNumber.toString().contains(query, ignoreCase = true) ||
            (order.customerName?.contains(query, ignoreCase = true) == true)
        val matchesStatus = selectedStatus?.let { order.status == it } ?: true
        matchesQuery && matchesStatus
    }.sortedByDescending { it.tokenNumber }

    Column(
        modifier = Modifier
            .fillMaxSize()
            .background(screenBackground)
            .padding(24.dp)
    ) {
        RoleBoardHeader(
            title = title,
            description = description,
            actionButtonLabel = if (onAddOrder != null) "Add Order" else null,
            onActionClick = onAddOrder,
            onLogout = onLogout
        )
        Spacer(modifier = Modifier.height(16.dp))
        BoardSearchField(
            value = searchQuery,
            onValueChange = { searchQuery = it },
            placeholder = "Search by token or customer"
        )
        Spacer(modifier = Modifier.height(12.dp))
        StatusFilterRow(
            availableStatuses = DemoOrderStatus.values().toList(),
            selectedStatus = selectedStatus,
            onSelected = { selectedStatus = it }
        )
        Spacer(modifier = Modifier.height(16.dp))
        if (orders.isEmpty()) {
            Box(modifier = Modifier.fillMaxSize(), contentAlignment = Alignment.Center) {
                Text("No orders match the current filters", color = MaterialTheme.colorScheme.onSurfaceVariant)
            }
        } else {
            LazyVerticalGrid(
                columns = GridCells.Adaptive(if (uiState.isPortrait) 260.dp else 320.dp),
                verticalArrangement = Arrangement.spacedBy(16.dp),
                horizontalArrangement = Arrangement.spacedBy(16.dp),
                modifier = Modifier.fillMaxSize()
            ) {
                items(orders, key = { it.id }) { order ->
                    CounterOrderCard(
                        order = order,
                        onAddOrder = onAddOrder,
                        onEditOrder = onEditOrder,
                        onKitchenAction = { orderId, lineId, status ->
                            OrderStore.markKitchenLine(orderId, lineId, status)
                        }
                    )
                }
            }
        }
    }
}

@Composable
private fun CounterOrderCard(
    order: DemoOrder,
    onAddOrder: (() -> Unit)?,
    onEditOrder: ((String) -> Unit)?,
    onKitchenAction: ((String, String, KitchenLineStatus) -> Unit)? = null
) {
    val totals = order.totals()
    ElevatedCard(modifier = Modifier.fillMaxWidth()) {
        Column(modifier = Modifier.padding(16.dp)) {
            Row(verticalAlignment = Alignment.CenterVertically, horizontalArrangement = Arrangement.SpaceBetween, modifier = Modifier.fillMaxWidth()) {
                Column(modifier = Modifier.weight(1f)) {
                    Text("Token #${order.tokenNumber}", style = MaterialTheme.typography.titleMedium, color = MaterialTheme.colorScheme.onSurface)
                    Text(order.createdAt, style = MaterialTheme.typography.bodySmall, color = MaterialTheme.colorScheme.onSurfaceVariant)
                }
                StatusBadge(order.status)
            }
            Spacer(modifier = Modifier.height(8.dp))
            Text(orderTypeLabel(order), style = MaterialTheme.typography.bodyMedium, fontWeight = FontWeight.SemiBold)
            order.customerName?.let {
                Text(it, style = MaterialTheme.typography.bodySmall, color = MaterialTheme.colorScheme.onSurfaceVariant)
            }
            Spacer(modifier = Modifier.height(8.dp))
            order.items.take(3).forEach { line ->
                Text(
                    text = " ${line.itemName} x ${line.quantity} ${line.unitLabel}",
                    style = MaterialTheme.typography.bodySmall,
                    color = MaterialTheme.colorScheme.onSurface
                )
            }
            if (order.items.size > 3) {
                Text("+${order.items.size - 3} more", style = MaterialTheme.typography.labelSmall, color = MaterialTheme.colorScheme.onSurfaceVariant)
            }
            Spacer(modifier = Modifier.height(12.dp))
            Row(verticalAlignment = Alignment.CenterVertically, horizontalArrangement = Arrangement.SpaceBetween, modifier = Modifier.fillMaxWidth()) {
                Column {
                    Text("Total", style = MaterialTheme.typography.labelSmall, color = MaterialTheme.colorScheme.onSurfaceVariant)
                    Text(totals.total.formatMoney(), style = MaterialTheme.typography.titleSmall, fontWeight = FontWeight.Bold)
                }
                if (order.status != DemoOrderStatus.PAID) {
                    TextButton(onClick = {
                        onEditOrder?.invoke(order.id) ?: onAddOrder?.invoke()
                    }) { Text("Edit order") }
                }
            }
            val kitchenLines = order.items.filter { it.source == "KITCHEN" }
            if (kitchenLines.isNotEmpty()) {
                Spacer(modifier = Modifier.height(8.dp))
                Text("Kitchen", style = MaterialTheme.typography.labelSmall, color = MaterialTheme.colorScheme.onSurfaceVariant)
                kitchenLines.forEach { line ->
                    Column(
                        modifier = Modifier
                            .fillMaxWidth()
                            .padding(vertical = 6.dp)
                            .border(1.dp, MaterialTheme.colorScheme.surfaceVariant, RoundedCornerShape(12.dp))
                            .padding(10.dp)
                    ) {
                        Text(line.itemName, fontWeight = FontWeight.SemiBold)
                        Text("Qty: ${line.quantity} ${line.unitLabel}", style = MaterialTheme.typography.bodySmall, color = MaterialTheme.colorScheme.onSurfaceVariant)
                        Spacer(modifier = Modifier.height(4.dp))
                        Row(verticalAlignment = Alignment.CenterVertically, horizontalArrangement = Arrangement.spacedBy(8.dp)) {
                            KitchenStatusBadge(line.kitchenStatus)
                            if (order.status != DemoOrderStatus.PAID && onKitchenAction != null) {
                                KitchenActionButtons(line) { status ->
                                    onKitchenAction(order.id, line.lineId, status)
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

@Composable
private fun KitchenActionButtons(line: DemoOrderLine, onUpdate: (KitchenLineStatus) -> Unit) {
    Row(horizontalArrangement = Arrangement.spacedBy(4.dp)) {
        if (line.kitchenStatus != KitchenLineStatus.IN_PROGRESS && line.kitchenStatus != KitchenLineStatus.FULFILLED && line.kitchenStatus != KitchenLineStatus.UNABLE) {
            TextButton(onClick = { onUpdate(KitchenLineStatus.IN_PROGRESS) }) { Text("Start") }
        }
        if (line.kitchenStatus != KitchenLineStatus.FULFILLED) {
            TextButton(onClick = { onUpdate(KitchenLineStatus.FULFILLED) }) { Text("Fulfil") }
        }
        if (line.kitchenStatus != KitchenLineStatus.UNABLE) {
            TextButton(onClick = { onUpdate(KitchenLineStatus.UNABLE) }) { Text("Unable") }
        }
    }
}

@OptIn(ExperimentalFoundationApi::class, ExperimentalMaterial3Api::class)
@Composable
fun KitchenCounterBoard(
    uiState: UiState,
    onLogout: () -> Unit
) {
    var statusFilter by remember { mutableStateOf<KitchenLineStatus?>(null) }
    val orders = OrderStore.ordersNeedingKitchen()
        .map { order ->
            val lines = order.items.filter { line ->
                if (line.source != "KITCHEN") false else statusFilter == null ||
                    line.kitchenStatus == statusFilter ||
                    (statusFilter == KitchenLineStatus.PENDING && line.kitchenStatus == null)
            }
            order.copy(items = lines)
        }
        .filter { it.items.isNotEmpty() }

    Column(
        modifier = Modifier
            .fillMaxSize()
            .background(screenBackground)
            .padding(24.dp)
    ) {
        RoleBoardHeader(
            title = "Kitchen Counter",
            description = "Track all kitchen bound lines and update progress.",
            onLogout = onLogout
        )
        Spacer(modifier = Modifier.height(16.dp))
        KitchenStatusRow(selected = statusFilter, onSelected = { statusFilter = it })
        Spacer(modifier = Modifier.height(16.dp))
        if (orders.isEmpty()) {
            Box(modifier = Modifier.fillMaxSize(), contentAlignment = Alignment.Center) {
                Text("Kitchen is clear!", color = MaterialTheme.colorScheme.onSurfaceVariant)
            }
        } else {
            LazyVerticalGrid(
                columns = GridCells.Adaptive(if (uiState.isPortrait) 300.dp else 360.dp),
                verticalArrangement = Arrangement.spacedBy(16.dp),
                horizontalArrangement = Arrangement.spacedBy(16.dp),
                modifier = Modifier.fillMaxSize()
            ) {
                items(orders, key = { it.id }) { order ->
                    KitchenOrderCard(order = order, onUpdate = { lineId, status -> OrderStore.markKitchenLine(order.id, lineId, status) })
                }
            }
        }
    }
}

@Composable
private fun KitchenOrderCard(order: DemoOrder, onUpdate: (String, KitchenLineStatus) -> Unit) {
    ElevatedCard(modifier = Modifier.fillMaxWidth()) {
        Column(modifier = Modifier.padding(16.dp)) {
            Row(verticalAlignment = Alignment.CenterVertically, horizontalArrangement = Arrangement.SpaceBetween, modifier = Modifier.fillMaxWidth()) {
                Column(modifier = Modifier.weight(1f)) {
                    Text("Token #${order.tokenNumber}", style = MaterialTheme.typography.titleMedium)
                    Text(order.createdAt, style = MaterialTheme.typography.bodySmall, color = MaterialTheme.colorScheme.onSurfaceVariant)
                }
                StatusBadge(order.status)
            }
            Spacer(modifier = Modifier.height(12.dp))
            order.items.forEach { line ->
                Column(
                    modifier = Modifier
                        .fillMaxWidth()
                        .padding(vertical = 8.dp)
                        .border(1.dp, MaterialTheme.colorScheme.surfaceVariant, RoundedCornerShape(12.dp))
                        .padding(12.dp)
                ) {
                    Text(line.itemName, fontWeight = FontWeight.SemiBold)
                    Text("Qty: ${line.quantity} ${line.unitLabel}", style = MaterialTheme.typography.bodySmall, color = MaterialTheme.colorScheme.onSurfaceVariant)
                    Spacer(modifier = Modifier.height(4.dp))
                    KitchenStatusBadge(line.kitchenStatus)
                }
            }
        }
    }
}

@OptIn(ExperimentalMaterial3Api::class, ExperimentalFoundationApi::class)
@Composable
fun CashCounterBoard(
    uiState: UiState,
    onLogout: () -> Unit
) {
    var searchQuery by remember { mutableStateOf("") }
    var showPaid by remember { mutableStateOf<Boolean?>(null) }
    var selectedOrderId by remember { mutableStateOf<String?>(null) }
    var selectedMethod by remember { mutableStateOf(PaymentMethod.CASH) }
    var reference by remember { mutableStateOf("") }
    val sheetState = rememberModalBottomSheetState(skipPartiallyExpanded = true)
    var showSheet by remember { mutableStateOf(false) }
    val scope = rememberCoroutineScope()

    val query = searchQuery.trim()
    val orders = OrderStore.orders.filter { order ->
        val queryMatches = query.isBlank() ||
            order.tokenNumber.toString().contains(query, ignoreCase = true) ||
            (order.customerName?.contains(query, ignoreCase = true) == true)
        val paymentMatches = when (showPaid) {
            true -> order.status == DemoOrderStatus.PAID
            false -> order.status != DemoOrderStatus.PAID
            null -> true
        }
        queryMatches && paymentMatches
    }

    Column(
        modifier = Modifier
            .fillMaxSize()
            .background(screenBackground)
            .padding(24.dp)
    ) {
        RoleBoardHeader(
            title = "Cash Counter",
            description = "Collect payments, add parcel charges and create invoices.",
            onLogout = onLogout
        )
        Spacer(modifier = Modifier.height(16.dp))
        BoardSearchField(value = searchQuery, onValueChange = { searchQuery = it }, placeholder = "Search by token or customer")
        Spacer(modifier = Modifier.height(12.dp))
        PaymentFilterRow(showPaid = showPaid, onFilterChange = { showPaid = it })
        Spacer(modifier = Modifier.height(16.dp))
        if (orders.isEmpty()) {
            Box(modifier = Modifier.fillMaxSize(), contentAlignment = Alignment.Center) {
                Text("No invoices to display", color = MaterialTheme.colorScheme.onSurfaceVariant)
            }
        } else {
            LazyVerticalGrid(
                columns = GridCells.Adaptive(if (uiState.isPortrait) 260.dp else 320.dp),
                verticalArrangement = Arrangement.spacedBy(16.dp),
                horizontalArrangement = Arrangement.spacedBy(16.dp),
                modifier = Modifier.fillMaxSize()
            ) {
                items(orders, key = { it.id }) { order ->
                    CashOrderCard(order = order, onCollect = {
                        selectedOrderId = order.id
                        selectedMethod = order.payment?.method ?: PaymentMethod.CASH
                        reference = order.payment?.reference.orEmpty()
                        showSheet = true
                        scope.launch { sheetState.show() }
                    })
                }
            }
        }
    }

    if (showSheet) {
        val order = OrderStore.orders.firstOrNull { it.id == selectedOrderId }
        if (order != null) {
            ModalBottomSheet(
                onDismissRequest = {
                    scope.launch { sheetState.hide() }.invokeOnCompletion {
                        showSheet = false
                        selectedOrderId = null
                    }
                },
                sheetState = sheetState
            ) {
                PaymentSheet(
                    order = order,
                    selectedMethod = selectedMethod,
                    onMethodChange = { selectedMethod = it },
                    reference = reference,
                    onReferenceChange = { reference = it },
                    onConfirm = {
                        if (order.status != DemoOrderStatus.PAID) {
                            OrderStore.markPaid(order.id, selectedMethod, reference)
                        }
                        scope.launch { sheetState.hide() }.invokeOnCompletion {
                            showSheet = false
                            selectedOrderId = null
                        }
                    }
                )
            }
        }
    }
}

@Composable
private fun CashOrderCard(order: DemoOrder, onCollect: () -> Unit) {
    val totals = order.totals()
    ElevatedCard(modifier = Modifier.fillMaxWidth()) {
        Column(modifier = Modifier.padding(16.dp)) {
            Row(verticalAlignment = Alignment.CenterVertically, horizontalArrangement = Arrangement.SpaceBetween, modifier = Modifier.fillMaxWidth()) {
                Column(modifier = Modifier.weight(1f)) {
                    Text("Token #${order.tokenNumber}", style = MaterialTheme.typography.titleMedium)
                    Text(order.createdAt, style = MaterialTheme.typography.bodySmall, color = MaterialTheme.colorScheme.onSurfaceVariant)
                }
                StatusBadge(order.status)
            }
            Spacer(modifier = Modifier.height(8.dp))
            Text("Due: ${totals.total.formatMoney()}", style = MaterialTheme.typography.bodyLarge, fontWeight = FontWeight.Bold)
            order.invoiceNumber?.let {
                Text("Invoice $it", style = MaterialTheme.typography.labelSmall, color = MaterialTheme.colorScheme.onSurfaceVariant)
            }
            Spacer(modifier = Modifier.height(8.dp))
            if (order.status == DemoOrderStatus.PAID) {
                TextButton(onClick = onCollect) { Text("View summary") }
            } else {
                Button(onClick = onCollect) { Text("Collect payment") }
            }
        }
    }
}

@OptIn(ExperimentalMaterial3Api::class)
@Composable
private fun PaymentSheet(
    order: DemoOrder,
    selectedMethod: PaymentMethod,
    onMethodChange: (PaymentMethod) -> Unit,
    reference: String,
    onReferenceChange: (String) -> Unit,
    onConfirm: () -> Unit
) {
    val totals = order.totals()
    Column(modifier = Modifier.padding(horizontal = 24.dp, vertical = 16.dp)) {
        Text("Token #${order.tokenNumber}", style = MaterialTheme.typography.titleLarge, fontWeight = FontWeight.Bold)
        Text(orderTypeLabel(order), style = MaterialTheme.typography.bodyMedium, color = MaterialTheme.colorScheme.onSurfaceVariant)
        Spacer(modifier = Modifier.height(16.dp))
        LazyColumn(
            modifier = Modifier
                .fillMaxWidth()
                .requiredHeightIn(max = 260.dp)
        ) {
            items(order.items, key = { it.lineId }) { line ->
                Row(modifier = Modifier.fillMaxWidth(), horizontalArrangement = Arrangement.SpaceBetween) {
                    Column(modifier = Modifier.weight(1f)) {
                        Text(line.itemName, fontWeight = FontWeight.SemiBold)
                        Text("${line.quantity} ${line.unitLabel}", style = MaterialTheme.typography.bodySmall, color = MaterialTheme.colorScheme.onSurfaceVariant)
                    }
                    Text(line.gross(order.orderType).formatMoney())
                }
                Divider(modifier = Modifier.padding(vertical = 8.dp))
            }
        }
        Spacer(modifier = Modifier.height(16.dp))
        SummaryRow(label = "Subtotal", value = totals.subtotalBeforeTax)
        SummaryRow(label = "SGST", value = totals.sgst)
        SummaryRow(label = "CGST", value = totals.cgst)
        if (totals.parcelCharge > 0) SummaryRow(label = "Parcel", value = totals.parcelCharge)
        Divider(modifier = Modifier.padding(vertical = 8.dp))
        SummaryRow(label = "Total", value = totals.total, bold = true)
        Spacer(modifier = Modifier.height(16.dp))
        Text("Payment method", style = MaterialTheme.typography.titleSmall)
        Spacer(modifier = Modifier.height(8.dp))
        Row(horizontalArrangement = Arrangement.spacedBy(8.dp)) {
            PaymentMethod.values().forEach { method ->
                FilterChip(
                    selected = selectedMethod == method,
                    onClick = { onMethodChange(method) },
                    label = { Text(method.name.lowercase().replaceFirstChar { it.uppercase() }) }
                )
            }
        }
        Spacer(modifier = Modifier.height(12.dp))
        OutlinedTextField(
            value = reference,
            onValueChange = onReferenceChange,
            label = { Text("Reference / UTR (optional)") },
            modifier = Modifier.fillMaxWidth()
        )
        Spacer(modifier = Modifier.height(16.dp))
        Button(onClick = onConfirm, modifier = Modifier.fillMaxWidth()) {
            Text(if (order.status == DemoOrderStatus.PAID) "Close" else "Mark paid")
        }
    }
}

@Composable
fun OwnerOrdersBoard(
    uiState: UiState,
    onLogout: () -> Unit
) {
    var counterFilter by remember { mutableStateOf<CounterStation?>(null) }
    val orders = OrderStore.orders.filter { counterFilter == null || it.counter == counterFilter }

    Column(
        modifier = Modifier
            .fillMaxSize()
            .background(screenBackground)
            .padding(24.dp)
    ) {
        RoleBoardHeader(
            title = "Owner Overview",
            description = "Monitor every token across counters.",
            onLogout = onLogout
        )
        Spacer(modifier = Modifier.height(16.dp))
        CounterFilterRow(counterFilter = counterFilter, onFilter = { counterFilter = it })
        Spacer(modifier = Modifier.height(16.dp))
        val spacing = if (uiState.isPortrait) 12.dp else 16.dp
        if (orders.isEmpty()) {
            Box(modifier = Modifier.fillMaxSize(), contentAlignment = Alignment.Center) {
                Text("No orders right now", color = MaterialTheme.colorScheme.onSurfaceVariant)
            }
        } else {
            LazyColumn(verticalArrangement = Arrangement.spacedBy(spacing), modifier = Modifier.fillMaxSize()) {
                items(orders, key = { it.id }) { order ->
                    ElevatedCard(modifier = Modifier.fillMaxWidth()) {
                        Column(modifier = Modifier.padding(16.dp)) {
                            Row(verticalAlignment = Alignment.CenterVertically, horizontalArrangement = Arrangement.SpaceBetween, modifier = Modifier.fillMaxWidth()) {
                                Column(modifier = Modifier.weight(1f)) {
                                    Text("Token #${order.tokenNumber}", style = MaterialTheme.typography.titleMedium)
                                    Text(order.createdAt, style = MaterialTheme.typography.bodySmall, color = MaterialTheme.colorScheme.onSurfaceVariant)
                                }
                                StatusBadge(order.status)
                            }
                            Spacer(modifier = Modifier.height(8.dp))
                            Text(counterLabel(order.counter), style = MaterialTheme.typography.bodyMedium, fontWeight = FontWeight.SemiBold)
                            order.customerName?.let {
                                Text(it, style = MaterialTheme.typography.bodySmall, color = MaterialTheme.colorScheme.onSurfaceVariant)
                            }
                            Spacer(modifier = Modifier.height(8.dp))
                            SummaryRow(label = "Value", value = order.totals().total, bold = true)
                            order.invoiceNumber?.let {
                                Text("Invoice $it", style = MaterialTheme.typography.labelSmall, color = MaterialTheme.colorScheme.onSurfaceVariant)
                            }
                        }
                    }
                }
            }
        }
    }
}

@Composable
private fun RoleBoardHeader(
    title: String,
    description: String,
    actionButtonLabel: String? = null,
    onActionClick: (() -> Unit)? = null,
    onLogout: () -> Unit
) {
    Column(modifier = Modifier.fillMaxWidth()) {
        Row(verticalAlignment = Alignment.Top, horizontalArrangement = Arrangement.SpaceBetween, modifier = Modifier.fillMaxWidth()) {
            Column(modifier = Modifier.weight(1f)) {
                Text(title, style = MaterialTheme.typography.headlineSmall, color = primary)
                Text(description, style = MaterialTheme.typography.bodyMedium, color = MaterialTheme.colorScheme.onSurfaceVariant, modifier = Modifier.padding(top = 4.dp))
            }
            LogoutActionIcon(onLogout = onLogout)
        }
        if (actionButtonLabel != null && onActionClick != null) {
            Spacer(modifier = Modifier.height(12.dp))
            Button(onClick = onActionClick, shape = RoundedCornerShape(12.dp)) {
                Text(actionButtonLabel)
            }
        }
    }
}

@Composable
private fun LogoutActionIcon(onLogout: () -> Unit) {
    var showDialog by remember { mutableStateOf(false) }
    IconButton(onClick = { showDialog = true }) {
        Icon(imageVector = Icons.Default.PowerSettingsNew, contentDescription = "Logout", tint = MaterialTheme.colorScheme.error)
    }
    if (showDialog) {
        AlertDialog(
            onDismissRequest = { showDialog = false },
            title = { Text("Confirm Logout") },
            text = { Text("Are you sure ?") },
            confirmButton = {
                TextButton(onClick = {
                    showDialog = false
                    onLogout()
                }) { Text("Yes") }
            },
            dismissButton = {
                TextButton(onClick = { showDialog = false }) { Text("No") }
            }
        )
    }
}

@Composable
private fun BoardSearchField(value: String, onValueChange: (String) -> Unit, placeholder: String) {
    OutlinedTextField(
        value = value,
        onValueChange = onValueChange,
        label = { Text(placeholder) },
        modifier = Modifier.fillMaxWidth()
    )
}

@OptIn(ExperimentalMaterial3Api::class)
@Composable
private fun StatusFilterRow(
    availableStatuses: List<DemoOrderStatus>,
    selectedStatus: DemoOrderStatus?,
    onSelected: (DemoOrderStatus?) -> Unit
) {
    Row(horizontalArrangement = Arrangement.spacedBy(8.dp)) {
        FilterChip(
            selected = selectedStatus == null,
            onClick = { onSelected(null) },
            label = { Text("All") }
        )
        availableStatuses.forEach { status ->
            FilterChip(
                selected = selectedStatus == status,
                onClick = { onSelected(status) },
                label = { Text(status.name.lowercase().replaceFirstChar { it.uppercase() }) }
            )
        }
    }
}

@OptIn(ExperimentalMaterial3Api::class)
@Composable
private fun KitchenStatusRow(selected: KitchenLineStatus?, onSelected: (KitchenLineStatus?) -> Unit) {
    Row(horizontalArrangement = Arrangement.spacedBy(8.dp)) {
        FilterChip(selected = selected == null, onClick = { onSelected(null) }, label = { Text("All items") })
        KitchenLineStatus.values().forEach { status ->
            FilterChip(selected = selected == status, onClick = { onSelected(status) }, label = { Text(statusLabel(status)) })
        }
    }
}

@Composable
private fun KitchenStatusBadge(status: KitchenLineStatus?) {
    val label = statusLabel(status ?: KitchenLineStatus.PENDING)
    val colors = MaterialTheme.colorScheme
    val background = when (status) {
        KitchenLineStatus.FULFILLED -> colors.secondaryContainer
        KitchenLineStatus.IN_PROGRESS -> colors.tertiaryContainer
        KitchenLineStatus.UNABLE -> colors.errorContainer
        else -> colors.surfaceVariant
    }
    val foreground = when (status) {
        KitchenLineStatus.UNABLE -> colors.error
        KitchenLineStatus.FULFILLED -> colors.secondary
        KitchenLineStatus.IN_PROGRESS -> colors.tertiary
        else -> colors.onSurfaceVariant
    }
    Surface(color = background, shape = RoundedCornerShape(50)) {
        Text(label, modifier = Modifier.padding(horizontal = 12.dp, vertical = 4.dp), color = foreground, style = MaterialTheme.typography.labelSmall)
    }
}

private fun statusLabel(status: KitchenLineStatus): String = when (status) {
    KitchenLineStatus.PENDING -> "Pending"
    KitchenLineStatus.IN_PROGRESS -> "In progress"
    KitchenLineStatus.FULFILLED -> "Ready"
    KitchenLineStatus.UNABLE -> "Unable"
}

@OptIn(ExperimentalMaterial3Api::class)
@Composable
private fun PaymentFilterRow(showPaid: Boolean?, onFilterChange: (Boolean?) -> Unit) {
    Row(horizontalArrangement = Arrangement.spacedBy(8.dp)) {
        FilterChip(selected = showPaid == null, onClick = { onFilterChange(null) }, label = { Text("All") })
        FilterChip(selected = showPaid == false, onClick = { onFilterChange(false) }, label = { Text("Pending") })
        FilterChip(selected = showPaid == true, onClick = { onFilterChange(true) }, label = { Text("Paid") })
    }
}

@OptIn(ExperimentalMaterial3Api::class)
@Composable
private fun CounterFilterRow(counterFilter: CounterStation?, onFilter: (CounterStation?) -> Unit) {
    Row(horizontalArrangement = Arrangement.spacedBy(8.dp)) {
        FilterChip(selected = counterFilter == null, onClick = { onFilter(null) }, label = { Text("All counters") })
        CounterStation.values().forEach { station ->
            FilterChip(selected = counterFilter == station, onClick = { onFilter(station) }, label = { Text(counterLabel(station)) })
        }
    }
}

private fun counterLabel(station: CounterStation): String = when (station) {
    CounterStation.SWEET_SNACK -> "Sweet & Snack"
    CounterStation.MAIN_COURSE -> "Main Course"
}

@Composable
private fun StatusBadge(status: DemoOrderStatus) {
    val scheme = MaterialTheme.colorScheme
    val (container, content) = when (status) {
        DemoOrderStatus.OPEN -> scheme.primary.copy(alpha = 0.15f) to scheme.primary
        DemoOrderStatus.PREPARING -> scheme.tertiaryContainer to scheme.tertiary
        DemoOrderStatus.READY -> Color(0xFFE0F7EC) to Color(0xFF0F9D58)
        DemoOrderStatus.BILLED -> scheme.secondaryContainer to scheme.secondary
        DemoOrderStatus.PAID -> Color(0xFFE6F2FF) to scheme.primary
    }
    Surface(color = container, shape = RoundedCornerShape(50)) {
        Text(status.name.lowercase().replaceFirstChar { it.uppercase() }, modifier = Modifier.padding(horizontal = 12.dp, vertical = 4.dp), color = content, style = MaterialTheme.typography.labelSmall)
    }
}

private fun orderTypeLabel(order: DemoOrder): String {
    return if (order.orderType == OrderType.STORE) {
        if (order.parcel) "Store  Parcel" else "Store  Dine-in"
    } else {
        val platform = order.platform?.name?.lowercase()?.replaceFirstChar { it.uppercase() } ?: "Online"
        val partner = order.partnerCode?.let { " #$it" }.orEmpty()
        "$platform$partner"
    }
}

@Composable
private fun SummaryRow(label: String, value: Double, bold: Boolean = false) {
    Row(horizontalArrangement = Arrangement.SpaceBetween, modifier = Modifier.fillMaxWidth()) {
        Text(label, fontWeight = if (bold) FontWeight.Bold else FontWeight.Normal)
        Text(value.formatMoney(), fontWeight = if (bold) FontWeight.Bold else FontWeight.Normal)
    }
}
