package com.kalingas.pos.dev.datasource.cache

import app.cash.sqldelight.Query
import app.cash.sqldelight.SuspendingTransacterImpl
import app.cash.sqldelight.db.QueryResult
import app.cash.sqldelight.db.SqlCursor
import app.cash.sqldelight.db.SqlDriver
import kotlin.Any
import kotlin.ByteArray
import kotlin.Long
import kotlin.String

public class KalingasDatabaseQueries(
  driver: SqlDriver,
) : SuspendingTransacterImpl(driver) {
  public fun getImage(url: String): Query<ByteArray> = GetImageQuery(url) { cursor ->
    cursor.getBytes(0)!!
  }

  public fun countImagesCache(): Query<Long> = Query(1_328_841_581, arrayOf("imageCache"), driver,
      "KalingasDatabase.sq", "countImagesCache", "SELECT COUNT(*) FROM imageCache") { cursor ->
    cursor.getLong(0)!!
  }

  /**
   * @return The number of rows updated.
   */
  public suspend fun updateImageCache(
    url: String,
    image: ByteArray,
    last_accessed: Long,
  ): Long {
    val result = driver.execute(762_722_466, """
        |INSERT OR REPLACE
        |INTO imageCache (
        |    url,
        |    image,
        |    last_accessed
        |) VALUES (?, ?, ?)
        """.trimMargin(), 3) {
          bindString(0, url)
          bindBytes(1, image)
          bindLong(2, last_accessed)
        }.await()
    notifyQueries(762_722_466) { emit ->
      emit("imageCache")
    }
    return result
  }

  /**
   * @return The number of rows updated.
   */
  public suspend fun updateAccessTime(last_accessed: Long, url: String): Long {
    val result = driver.execute(1_089_907_724,
        """UPDATE imageCache SET last_accessed = ? WHERE url = ?""", 2) {
          bindLong(0, last_accessed)
          bindString(1, url)
        }.await()
    notifyQueries(1_089_907_724) { emit ->
      emit("imageCache")
    }
    return result
  }

  /**
   * @return The number of rows updated.
   */
  public suspend fun evictOldest(`value`: Long): Long {
    val result = driver.execute(1_437_945_942, """
        |DELETE FROM imageCache WHERE url IN (
        |    SELECT url FROM imageCache ORDER BY last_accessed ASC LIMIT ?
        |)
        """.trimMargin(), 1) {
          bindLong(0, value)
        }.await()
    notifyQueries(1_437_945_942) { emit ->
      emit("imageCache")
    }
    return result
  }

  /**
   * @return The number of rows updated.
   */
  public suspend fun clearImageCache(): Long {
    val result = driver.execute(1_476_112_066, """DELETE FROM imageCache""", 0).await()
    notifyQueries(1_476_112_066) { emit ->
      emit("imageCache")
    }
    return result
  }

  private inner class GetImageQuery<out T : Any>(
    public val url: String,
    mapper: (SqlCursor) -> T,
  ) : Query<T>(mapper) {
    override fun addListener(listener: Query.Listener) {
      driver.addListener("imageCache", listener = listener)
    }

    override fun removeListener(listener: Query.Listener) {
      driver.removeListener("imageCache", listener = listener)
    }

    override fun <R> execute(mapper: (SqlCursor) -> QueryResult<R>): QueryResult<R> =
        driver.executeQuery(-1_527_081_545, """
    |SELECT image
    |FROM imageCache
    |WHERE url = ?
    """.trimMargin(), mapper, 1) {
      bindString(0, url)
    }

    override fun toString(): String = "KalingasDatabase.sq:getImage"
  }
}
