package com.kalingas.pos.dev.presentation.admin.manager

import com.kalingas.pos.dev.interactor.manager.StateManager
import com.kalingas.pos.dev.interactor.uistate.AdminSheet
import com.kalingas.pos.dev.interactor.uistate.CategoryFormState
import com.kalingas.pos.dev.interactor.uistate.FoodCategory
import com.kalingas.pos.dev.interactor.uistate.FoodItem
import com.kalingas.pos.dev.interactor.uistate.ItemField
import com.kalingas.pos.dev.interactor.uistate.ItemFormState
import com.kalingas.pos.dev.interactor.uistate.VariantFormState
import com.kalingas.pos.dev.interactor.uistate.ItemUnit
import com.kalingas.pos.dev.interactor.uistate.MqttField
import com.kalingas.pos.dev.presentation.admin.event.AdminUiEvents
import com.kalingas.pos.dev.util.isIpAddressError
import com.kalingas.pos.dev.util.isMqttUrlError
import com.kalingas.pos.dev.util.isPortError
import kotlin.math.absoluteValue
import kotlin.random.Random

object AdminManager {

    fun onTriggerEvent(event: AdminUiEvents) {
        when (event) {
            is AdminUiEvents.UpdateMqttField -> updateMqttField(event.field, event.value)
            AdminUiEvents.SaveMqttSettings -> saveMqttSettings()
            is AdminUiEvents.ToggleCategory -> toggleCategory(event.categoryId)
            is AdminUiEvents.ToggleItemActive -> toggleItemActive(event.categoryId, event.itemId)
            is AdminUiEvents.ShowCategorySheet -> showCategorySheet(event.categoryId)
            is AdminUiEvents.ShowItemSheet -> showItemSheet(event.categoryId, event.itemId)
            AdminUiEvents.DismissSheet -> dismissSheet()
            is AdminUiEvents.UpdateCategoryName -> updateCategoryForm { copy(name = event.value, nameError = false) }
            is AdminUiEvents.UpdateCategoryActive -> updateCategoryForm { copy(isActive = event.value) }
            AdminUiEvents.SaveCategoryForm -> saveCategoryForm()
            is AdminUiEvents.UpdateItemField -> updateBaseItemField(event.field, event.value)
            is AdminUiEvents.UpdateVariantField -> updateVariantField(event.index, event.field, event.value)
            is AdminUiEvents.UpdateVariantUnit -> updateVariantUnit(event.index, event.unit)
            is AdminUiEvents.UpdateVariantActive -> updateVariantActive(event.index, event.value)
            AdminUiEvents.AddVariant -> addVariant()
            is AdminUiEvents.RemoveVariant -> removeVariant(event.index)
            AdminUiEvents.SaveItemForm -> saveItemForm()
        }
    }

    private fun updateMqttField(field: MqttField, value: String) {
        val state = StateManager.getState()
        val form = state.mqttFormState
        val updatedForm = when (field) {
            MqttField.Url -> form.copy(mqttUrl = value)
            MqttField.Port -> form.copy(mqttPort = value)
            MqttField.WebPort -> form.copy(mqttWebPort = value)
            MqttField.Username -> form.copy(mqttUsername = value)
            MqttField.Password -> form.copy(mqttPassword = value)
            MqttField.Certificate -> form.copy(mqttCertificate = value)
            MqttField.LocalUrl -> form.copy(localBrokerUrl = value)
            MqttField.LocalPort -> form.copy(localBrokerPort = value)
            MqttField.LocalWebPort -> form.copy(localBrokerWebPort = value)
        }
        StateManager.updateState(
            state.copy(
                mqttFormState = updatedForm.copy(
                    errorFields = updatedForm.errorFields - field,
                    savedMessage = null
                )
            )
        )
    }

    private fun saveMqttSettings() {
        val state = StateManager.getState()
        val form = state.mqttFormState
        val missing = mutableSetOf<MqttField>()
        if (form.mqttUrl.isBlank() || isMqttUrlError(form.mqttUrl)) missing += MqttField.Url
        if (form.mqttPort.isBlank() || isPortError(form.mqttPort)) missing += MqttField.Port
        if (form.mqttWebPort.isBlank() || isPortError(form.mqttWebPort)) missing += MqttField.WebPort
        if (form.mqttUsername.isBlank()) missing += MqttField.Username
        if (form.mqttPassword.isBlank()) missing += MqttField.Password
        if (form.mqttCertificate.isBlank()) missing += MqttField.Certificate
        if (form.localBrokerUrl.isBlank() || isIpAddressError(form.localBrokerUrl)) missing += MqttField.LocalUrl
        if (form.localBrokerPort.isBlank() || isPortError(form.localBrokerPort)) missing += MqttField.LocalPort
        if (form.localBrokerWebPort.isBlank() || isPortError(form.localBrokerWebPort)) missing += MqttField.LocalWebPort

        val updated = form.copy(
            errorFields = missing,
            savedMessage = if (missing.isEmpty()) "Settings saved locally" else null
        )
        StateManager.updateState(state.copy(mqttFormState = updated))
    }

    private fun toggleCategory(categoryId: Int) {
        val state = StateManager.getState()
        val categories = state.foodCategories
        val clicked = categories.firstOrNull { it.id == categoryId } ?: return
        val hasMultipleExpanded = categories.count { it.isExpanded } > 1
        val shouldCollapseSelected = clicked.isExpanded && !hasMultipleExpanded

        val updated = categories.map { category ->
            when {
                category.id == categoryId -> category.copy(isExpanded = !shouldCollapseSelected)
                shouldCollapseSelected -> category
                category.isExpanded -> category.copy(isExpanded = false)
                else -> category
            }
        }
        StateManager.updateState(state.copy(foodCategories = updated))
    }

    private fun toggleItemActive(categoryId: Int, itemId: String) {
        val state = StateManager.getState()
        val updated = state.foodCategories.map { category ->
            if (category.id != categoryId) return@map category
            val newItems = category.items.map { item ->
                if (item.id == itemId) item.copy(isActive = !item.isActive) else item
            }
            category.copy(items = newItems)
        }
        StateManager.updateState(state.copy(foodCategories = updated))
    }

    private fun showCategorySheet(categoryId: Int?) {
        val state = StateManager.getState()
        val form = if (categoryId == null) {
            CategoryFormState()
        } else {
            val category = state.foodCategories.firstOrNull { it.id == categoryId }
            if (category == null) CategoryFormState() else CategoryFormState(category.id, category.name, category.isActive)
        }
        StateManager.updateState(state.copy(adminSheet = AdminSheet.Category(form)))
    }

    private fun showItemSheet(categoryId: Int, itemId: String?) {
        val state = StateManager.getState()
        val category = state.foodCategories.firstOrNull { it.id == categoryId } ?: return
        val form = if (itemId == null) {
            ItemFormState(
                categoryId = categoryId,
                categoryName = category.name,
                variants = listOf(defaultVariantForm())
            )
        } else {
            val item = category.items.firstOrNull { it.id == itemId } ?: return
            val siblings = category.items.filter { it.name == item.name }
            ItemFormState(
                categoryId = categoryId,
                categoryName = category.name,
                name = item.name,
                originalName = item.name,
                variants = siblings.map { it.toVariantFormState() }
            )
        }
        StateManager.updateState(state.copy(adminSheet = AdminSheet.Item(form)))
    }

    private fun dismissSheet() {
        val state = StateManager.getState()
        StateManager.updateState(state.copy(adminSheet = null))
    }

    private fun updateCategoryForm(transform: CategoryFormState.() -> CategoryFormState) {
        val state = StateManager.getState()
        val sheet = state.adminSheet
        if (sheet is AdminSheet.Category) {
            StateManager.updateState(state.copy(adminSheet = AdminSheet.Category(sheet.form.transform())))
        }
    }

    private fun updateItemForm(transform: ItemFormState.() -> ItemFormState) {
        val state = StateManager.getState()
        val sheet = state.adminSheet
        if (sheet is AdminSheet.Item) {
            StateManager.updateState(state.copy(adminSheet = AdminSheet.Item(sheet.form.transform())))
        }
    }

    private fun updateBaseItemField(field: ItemField, value: String) {
        updateItemForm {
            when (field) {
                ItemField.Name -> copy(name = value, nameError = false)
                else -> this
            }
        }
    }

    private fun updateVariantField(index: Int, field: ItemField, value: String) {
        updateItemForm {
            val updated = variants.mapIndexed { i, variant ->
                if (i != index) variant else variant.updateField(field, value)
            }
            copy(variants = updated)
        }
    }

    private fun updateVariantUnit(index: Int, unit: ItemUnit) {
        updateItemForm {
            val updated = variants.mapIndexed { i, variant ->
                if (i != index) variant else {
                    val defaultLabel = when (unit) {
                        ItemUnit.UNIT -> "unit"
                        ItemUnit.WEIGHT -> "kg"
                        ItemUnit.VOLUME -> "ltr"
                    }
                    variant.copy(
                        unit = unit,
                        unitLabel = if (variant.unitLabel.isBlank()) defaultLabel else variant.unitLabel
                    )
                }
            }
            copy(variants = updated)
        }
    }

    private fun updateVariantActive(index: Int, value: Boolean) {
        updateItemForm {
            val updated = variants.mapIndexed { i, variant ->
                if (i != index) variant else variant.copy(isActive = value)
            }
            copy(variants = updated)
        }
    }

    private fun addVariant() {
        updateItemForm {
            copy(variants = variants + defaultVariantForm())
        }
    }

    private fun removeVariant(index: Int) {
        updateItemForm {
            if (variants.size <= 1) this else copy(variants = variants.toMutableList().also { it.removeAt(index) })
        }
    }

    private fun VariantFormState.updateField(field: ItemField, value: String): VariantFormState {
        val updatedErrors = errors - field
        return when (field) {
            ItemField.VariantName -> copy(variantName = value, errors = updatedErrors)
            ItemField.StorePrice -> copy(storePrice = value, errors = updatedErrors)
            ItemField.OnlinePrice -> copy(onlinePrice = value, errors = updatedErrors)
            ItemField.GstRate -> copy(gstRate = value, errors = updatedErrors)
            ItemField.Source -> copy(source = value, errors = updatedErrors)
            ItemField.UnitLabel -> copy(unitLabel = value, errors = updatedErrors)
            ItemField.QuantityStep -> copy(quantityStep = value, errors = updatedErrors)
            ItemField.MinStoreQuantity -> copy(minStoreQuantity = value, errors = updatedErrors)
            ItemField.MinOnlineQuantity -> copy(minOnlineQuantity = value, errors = updatedErrors)
            ItemField.Name -> copy(variantName = value, errors = updatedErrors)
        }
    }

    private fun defaultVariantForm(): VariantFormState =
        VariantFormState(
            quantityStep = "1.0",
            minStoreQuantity = "1.0",
            minOnlineQuantity = "1.0"
        )

    private fun FoodItem.toVariantFormState(): VariantFormState =
        VariantFormState(
            id = id,
            variantName = variantName.orEmpty(),
            storePrice = storePrice.toString(),
            onlinePrice = onlinePrice.toString(),
            gstRate = gstRate.toString(),
            source = source,
            unit = unit,
            unitLabel = unitLabel,
            quantityStep = quantityStep.toString(),
            minStoreQuantity = minStoreQuantity.toString(),
            minOnlineQuantity = minOnlineQuantity.toString(),
            isActive = isActive,
            storePriceLabel = storePriceText,
            onlinePriceLabel = onlinePriceText
        )

    private fun saveCategoryForm() {
        val state = StateManager.getState()
        val sheet = state.adminSheet as? AdminSheet.Category ?: return
        val form = sheet.form
        if (form.name.isBlank()) {
            updateCategoryForm { copy(nameError = true) }
            return
        }
        val categories = state.foodCategories.toMutableList()
        if (form.id == null) {
            val nextId = (categories.maxOfOrNull { it.id } ?: 0) + 1
            categories += FoodCategory(
                id = nextId,
                name = form.name,
                items = emptyList(),
                isActive = form.isActive
            )
        } else {
            val index = categories.indexOfFirst { it.id == form.id }
            if (index != -1) {
                val existing = categories[index]
                categories[index] = existing.copy(name = form.name, isActive = form.isActive)
            }
        }
        StateManager.updateState(state.copy(foodCategories = categories, adminSheet = null))
    }

    private fun saveItemForm() {
        val state = StateManager.getState()
        val sheet = state.adminSheet as? AdminSheet.Item ?: return
        val form = sheet.form

        if (form.name.isBlank()) {
            updateItemForm { copy(nameError = true) }
            return
        }

        val variantResults = form.variants.map { variant ->
            val errors = mutableSetOf<ItemField>()
            val storePrice = variant.storePrice.toDoubleOrNull() ?: run { errors += ItemField.StorePrice; null }
            val onlinePrice = variant.onlinePrice.toDoubleOrNull() ?: run { errors += ItemField.OnlinePrice; null }
            val gst = variant.gstRate.toIntOrNull() ?: run { errors += ItemField.GstRate; null }
            val quantityStep = variant.quantityStep.toDoubleOrNull() ?: run { errors += ItemField.QuantityStep; null }
            val minStoreQty = variant.minStoreQuantity.toDoubleOrNull() ?: run { errors += ItemField.MinStoreQuantity; null }
            val minOnlineQty = variant.minOnlineQuantity.toDoubleOrNull() ?: run { errors += ItemField.MinOnlineQuantity; null }
            VariantValidationResult(
                variant = if (errors.isEmpty()) variant.copy(errors = emptySet()) else variant.copy(errors = errors),
                storePrice = storePrice,
                onlinePrice = onlinePrice,
                gst = gst,
                quantityStep = quantityStep,
                minStoreQty = minStoreQty,
                minOnlineQty = minOnlineQty
            )
        }

        if (variantResults.any { it.hasError }) {
            updateItemForm {
                copy(variants = variantResults.map { it.variant })
            }
            return
        }

        val categories = state.foodCategories.toMutableList()
        val categoryIndex = categories.indexOfFirst { it.id == form.categoryId }
        if (categoryIndex == -1) return

        val category = categories[categoryIndex]
        val filteredItems = category.items.filterNot {
            val originalName = form.originalName
            if (originalName != null) it.name == originalName else false
        }.toMutableList()

        variantResults.forEach { result ->
            val variant = result.variant
            filteredItems += FoodItem(
                id = variant.id ?: Random.nextLong().absoluteValue.toString(),
                name = form.name,
                storePrice = result.storePrice!!,
                onlinePrice = result.onlinePrice!!,
                gstRate = result.gst!!,
                source = variant.source,
                categoryId = form.categoryId,
                isActive = variant.isActive,
                unit = variant.unit,
                unitLabel = variant.unitLabel.ifBlank {
                    when (variant.unit) {
                        ItemUnit.UNIT -> "unit"
                        ItemUnit.WEIGHT -> "kg"
                        ItemUnit.VOLUME -> "ltr"
                    }
                },
                quantityStep = result.quantityStep!!,
                minStoreQuantity = result.minStoreQty!!,
                minOnlineQuantity = result.minOnlineQty!!,
                variantName = variant.variantName.ifBlank { null },
                storePriceText = variant.storePriceLabel.ifBlank { variant.storePrice },
                onlinePriceText = variant.onlinePriceLabel.ifBlank { variant.onlinePrice }
            )
        }

        categories[categoryIndex] = category.copy(items = filteredItems)
        StateManager.updateState(state.copy(foodCategories = categories, adminSheet = null))
    }

    private data class VariantValidationResult(
        val variant: VariantFormState,
        val storePrice: Double?,
        val onlinePrice: Double?,
        val gst: Int?,
        val quantityStep: Double?,
        val minStoreQty: Double?,
        val minOnlineQty: Double?,
    ) {
        val hasError: Boolean =
            storePrice == null || onlinePrice == null || gst == null || quantityStep == null || minStoreQty == null || minOnlineQty == null
    }
}
