package com.kalingas.pos.dev.interactor.manager

import com.kalingas.pos.dev.interactor.event.UiEvents
import com.kalingas.pos.dev.interactor.uistate.UiState
import com.kalingas.pos.dev.presentation.admin.manager.AdminManager
import com.kalingas.pos.dev.presentation.login.manager.LoginManager
import com.kalingas.pos.dev.presentation.navigation.manager.NavigationManager
import com.kalingas.pos.dev.presentation.order.manager.OrderManager
import com.kalingas.pos.dev.presentation.splash.manager.SplashManager
import kotlinx.coroutines.CoroutineScope
import kotlinx.coroutines.Dispatchers
import kotlinx.coroutines.flow.MutableStateFlow
import kotlinx.coroutines.flow.StateFlow
import kotlinx.coroutines.launch

object StateManager {

    private val _uiStateFlow = MutableStateFlow(UiState())
    val uiStateFlow: StateFlow<UiState> get() = _uiStateFlow

    private var scope: CoroutineScope? = null

    fun onTriggerEvent(event: UiEvents) {
        when (event) {
            is UiEvents.Portrait -> updateState(getState().copy(isPortrait = event.isPortrait))
            is UiEvents.SplashEvents -> SplashManager.onTriggerEvent(event.events)
            is UiEvents.NavigationEvents -> NavigationManager.onTriggerEvent(event.events)
            is UiEvents.LoginEvents -> LoginManager.onTriggerEvent(event.events)
            is UiEvents.AdminEvents -> AdminManager.onTriggerEvent(event.events)
            is UiEvents.OrderEvents -> OrderManager.onTriggerEvent(event.events)
        }
    }

    fun setViewModelScope(viewModelScope: CoroutineScope) {
        scope = viewModelScope
    }

    fun launchDefault(block: suspend () -> Unit) {
        scope?.launch(Dispatchers.Default) { block() }
    }

    fun launchMain(block: suspend () -> Unit) {
        scope?.launch { block() }
    }

    fun updateState(uiState: UiState) {
        _uiStateFlow.value = uiState
    }

    fun getState(): UiState = _uiStateFlow.value

    fun setDeepLink(deepLink: String?) {
        updateState(getState().copy(deepLink = deepLink))
    }
}
