import kalingasposdev.composeapp.generated.resources.Res
import kalingasposdev.composeapp.generated.resources.kaalingas
import org.jetbrains.compose.resources.painterResource

@Composable
fun LoginView(uiState: UiState, onTriggerEvent: (UiEvents) -> Unit) {
    var passwordVisible by remember { mutableStateOf(false) }
    var roleMenuExpanded by remember { mutableStateOf(false) }
    val selectedRole = uiState.selectedRole

    Column(
        modifier = Modifier
            .fillMaxSize()
            .background(screenBackground)
            .padding(horizontal = 24.dp, vertical = 32.dp),
        verticalArrangement = Arrangement.Center,
        horizontalAlignment = Alignment.CenterHorizontally
    ) {
        Image(
            painter = painterResource(Res.drawable.kaalingas),
            contentDescription = "Kalingas POS",
            modifier = Modifier.height(80.dp)
        )
        Spacer(modifier = Modifier.height(16.dp))
        Text(
            text = "Welcome back",
            style = MaterialTheme.typography.headlineSmall,
            color = primary
        )
        Spacer(modifier = Modifier.height(24.dp))

        Box(modifier = Modifier.fillMaxWidth()) {
            OutlinedTextField(
                modifier = Modifier
                    .fillMaxWidth(),
                value = selectedRole?.displayName ?: "Select user",
                onValueChange = {},
                readOnly = true,
                label = { Text("Username") },
                shape = RoundedCornerShape(12.dp),
                colors = kalingasOutlinedTextFieldColors()
            )
            Box(
                modifier = Modifier
                    .matchParentSize()
                    .clickable { roleMenuExpanded = true }
            )
            DropdownMenu(
                expanded = roleMenuExpanded,
                onDismissRequest = { roleMenuExpanded = false }
            ) {
                UserRole.entries.forEach { role ->
                    DropdownMenuItem(
                        text = { Text(role.displayName) },
                        onClick = {
                            roleMenuExpanded = false
                            onTriggerEvent(UiEvents.LoginEvents(LoginUiEvents.RoleSelected(role)))
                        }
                    )
                }
            }
        }

        Spacer(modifier = Modifier.height(16.dp))
        OutlinedTextField(
            value = uiState.loginPassword,
            onValueChange = { onTriggerEvent(UiEvents.LoginEvents(LoginUiEvents.PasswordChanged(it))) },
            label = { Text("Password") },
            modifier = Modifier.fillMaxWidth(),
            visualTransformation = if (passwordVisible) VisualTransformation.None else PasswordVisualTransformation(),
            trailingIcon = {
                Text(
                    text = if (passwordVisible) "Hide" else "Show",
                    color = primary,
                    modifier = Modifier
                        .padding(end = 8.dp)
                        .clickable { passwordVisible = !passwordVisible }
                )
            },
            colors = kalingasOutlinedTextFieldColors(),
            shape = RoundedCornerShape(12.dp)
        )

        uiState.loginError?.let {
            Spacer(modifier = Modifier.height(12.dp))
            Text(text = it, color = MaterialTheme.colorScheme.error)
        }

        Spacer(modifier = Modifier.height(24.dp))
        Button(
            enabled = selectedRole != null && uiState.loginPassword.isNotBlank(),
            onClick = { onTriggerEvent(UiEvents.LoginEvents(LoginUiEvents.SubmitCredentials)) }
        ) {
            Text("Sign In")
        }

        Spacer(modifier = Modifier.height(8.dp))
        TextButton(onClick = {
            onTriggerEvent(
                UiEvents.NavigationEvents(
                    NavigationUiEvents.Show(Screens.PosTestLab)
                )
            )
        }) {
            Text("Open POS Test Lab")
        }
    }
}
