package com.kalingas.pos.dev.presentation.login.event

import com.kalingas.pos.dev.interactor.uistate.UserRole

sealed class LoginUiEvents {
    data class RoleSelected(val role: UserRole) : LoginUiEvents()
    data class PasswordChanged(val value: String) : LoginUiEvents()
    data object SubmitCredentials : LoginUiEvents()
    data object Logout : LoginUiEvents()
}
