package com.kalingas.pos.dev.datasource.cache.composeApp

import app.cash.sqldelight.SuspendingTransacterImpl
import app.cash.sqldelight.db.AfterVersion
import app.cash.sqldelight.db.QueryResult
import app.cash.sqldelight.db.SqlDriver
import app.cash.sqldelight.db.SqlSchema
import com.kalingas.pos.dev.datasource.cache.KalingasDatabase
import com.kalingas.pos.dev.datasource.cache.KalingasDatabaseQueries
import kotlin.Long
import kotlin.Unit
import kotlin.reflect.KClass

internal val KClass<KalingasDatabase>.schema: SqlSchema<QueryResult.AsyncValue<Unit>>
  get() = KalingasDatabaseImpl.Schema

internal fun KClass<KalingasDatabase>.newInstance(driver: SqlDriver): KalingasDatabase =
    KalingasDatabaseImpl(driver)

private class KalingasDatabaseImpl(
  driver: SqlDriver,
) : SuspendingTransacterImpl(driver),
    KalingasDatabase {
  override val kalingasDatabaseQueries: KalingasDatabaseQueries = KalingasDatabaseQueries(driver)

  public object Schema : SqlSchema<QueryResult.AsyncValue<Unit>> {
    override val version: Long
      get() = 2

    override fun create(driver: SqlDriver): QueryResult.AsyncValue<Unit> = QueryResult.AsyncValue {
      driver.execute(null, """
          |CREATE TABLE IF NOT EXISTS imageCache (
          |    url TEXT NOT NULL PRIMARY KEY,
          |    image BLOB NOT NULL,
          |    last_accessed INTEGER NOT NULL
          |)
          """.trimMargin(), 0).await()
    }

    private fun migrateInternal(
      driver: SqlDriver,
      oldVersion: Long,
      newVersion: Long,
    ): QueryResult.AsyncValue<Unit> = QueryResult.AsyncValue {
      if (oldVersion <= 1 && newVersion > 1) {
        driver.execute(null, """
            |CREATE TABLE IF NOT EXISTS imageCache (
            |    url TEXT NOT NULL PRIMARY KEY,
            |    image BLOB NOT NULL,
            |    last_accessed INTEGER NOT NULL
            |)
            """.trimMargin(), 0).await()
      }
    }

    override fun migrate(
      driver: SqlDriver,
      oldVersion: Long,
      newVersion: Long,
      vararg callbacks: AfterVersion,
    ): QueryResult.AsyncValue<Unit> = QueryResult.AsyncValue {
      var lastVersion = oldVersion

      callbacks.filter { it.afterVersion in oldVersion until newVersion }
      .sortedBy { it.afterVersion }
      .forEach { callback ->
        migrateInternal(driver, oldVersion = lastVersion, newVersion = callback.afterVersion +
          1).await()
        callback.block(driver)
        lastVersion = callback.afterVersion + 1
      }

      if (lastVersion < newVersion) {
        migrateInternal(driver, lastVersion, newVersion).await()
      }
    }
  }
}
