package com.kalingas.pos.dev.util

private val mqttHostnameRegex = "^[a-zA-Z0-9.-]+$".toRegex()
private val ipv4Regex =
    ("^((25[0-5]|2[0-4]\\d|1\\d{2}|[1-9]?\\d)\\.){3}(25[0-5]|2[0-4]\\d|1\\d{2}|[1-9]?\\d)$").toRegex()

fun isValidMqttUrl(url: String): Boolean = url.matches(mqttHostnameRegex)

fun isMqttUrlError(text: String): Boolean = text.isNotEmpty() && !isValidMqttUrl(text)

fun isValidPort(port: String): Boolean = port.length == 4 && port.toIntOrNull() != null

fun isPortError(text: String): Boolean = text.isNotEmpty() && !isValidPort(text)

fun isValidIpAddress(address: String): Boolean = address.matches(ipv4Regex)

fun isIpAddressError(text: String): Boolean = text.isNotEmpty() && !isValidIpAddress(text)
