package com.kalingas.pos.dev

import androidx.compose.foundation.layout.fillMaxSize
import androidx.compose.material3.MaterialTheme
import androidx.compose.material3.Surface
import androidx.compose.runtime.Composable
import androidx.compose.runtime.DisposableEffect
import androidx.compose.runtime.collectAsState
import androidx.compose.runtime.getValue
import androidx.compose.runtime.remember
import androidx.compose.ui.Modifier
import com.kalingas.pos.dev.interactor.view.MainView
import com.kalingas.pos.dev.interactor.viewmodel.MainViewModel
import com.kalingas.pos.dev.theme.KalingasTheme
import org.jetbrains.compose.ui.tooling.preview.Preview

@Composable
@Preview
fun App(deepLink: String? = null) {
    KalingasTheme {
        val mainViewModel = remember(deepLink) { MainViewModel(deepLink) }
        DisposableEffect(mainViewModel) {
            onDispose { mainViewModel.clear() }
        }
        val uiState by mainViewModel.stateManager.uiStateFlow.collectAsState()
        Surface(modifier = Modifier.fillMaxSize(), color = MaterialTheme.colorScheme.background) {
            MainView(uiState) { event -> mainViewModel.onTriggerEvent(event) }
        }
    }
}
