package com.kalingas.pos.dev.interactor.view

import androidx.compose.foundation.background
import androidx.compose.foundation.layout.Box
import androidx.compose.foundation.layout.WindowInsets
import androidx.compose.foundation.layout.asPaddingValues
import androidx.compose.foundation.layout.fillMaxSize
import androidx.compose.foundation.layout.padding
import androidx.compose.foundation.layout.systemBars
import androidx.compose.ui.Alignment
import androidx.compose.material3.CircularProgressIndicator
import androidx.compose.material3.MaterialTheme
import androidx.compose.material3.Scaffold
import androidx.compose.runtime.Composable
import androidx.compose.ui.Modifier
import androidx.compose.ui.layout.onSizeChanged
import com.kalingas.pos.dev.interactor.event.UiEvents
import com.kalingas.pos.dev.interactor.uistate.UiState
import com.kalingas.pos.dev.presentation.Screens
import com.kalingas.pos.dev.presentation.admin.view.AdminHomeView
import com.kalingas.pos.dev.presentation.admin.view.AdminItemManagementView
import com.kalingas.pos.dev.presentation.admin.view.AdminMqttView
import com.kalingas.pos.dev.presentation.dashboard.view.CashCounterBoard
import com.kalingas.pos.dev.presentation.dashboard.view.CounterOrdersBoard
import com.kalingas.pos.dev.presentation.dashboard.view.CounterStation
import com.kalingas.pos.dev.presentation.dashboard.view.KitchenCounterBoard
import com.kalingas.pos.dev.presentation.dashboard.view.OwnerOrdersBoard
import com.kalingas.pos.dev.presentation.dashboard.view.PosTestLabView
import com.kalingas.pos.dev.presentation.dashboard.view.RoleDashboard
import com.kalingas.pos.dev.presentation.order.event.OrderUiEvents
import com.kalingas.pos.dev.presentation.order.view.AddEditOrderView
import com.kalingas.pos.dev.presentation.login.view.LoginView
import com.kalingas.pos.dev.presentation.login.event.LoginUiEvents
import com.kalingas.pos.dev.presentation.navigation.event.NavigationUiEvents
import com.kalingas.pos.dev.presentation.navigation.view.NavigationView
import com.kalingas.pos.dev.presentation.splash.view.SplashView
import com.kalingas.pos.dev.theme.primary

@Composable
fun MainView(uiState: UiState, onTriggerEvent: (UiEvents) -> Unit) {
    val logout: () -> Unit = { onTriggerEvent(UiEvents.LoginEvents(LoginUiEvents.Logout)) }
    val openSnackOrder: () -> Unit = {
        onTriggerEvent(UiEvents.OrderEvents(OrderUiEvents.StartNew(CounterStation.SWEET_SNACK, null)))
    }
    val openMainCourseOrder: () -> Unit = {
        onTriggerEvent(UiEvents.OrderEvents(OrderUiEvents.StartNew(CounterStation.MAIN_COURSE, null)))
    }

    Scaffold(containerColor = primary) { paddingValues ->
        Box(
            modifier = Modifier
                .fillMaxSize()
                .background(primary)
                .padding(paddingValues)
                .padding(WindowInsets.systemBars.asPaddingValues())
                .onSizeChanged { size -> onTriggerEvent(UiEvents.Portrait(size.width < size.height)) }
        ) {
            when (uiState.mainView) {
                Screens.Splash -> SplashView(uiState, onTriggerEvent)
                Screens.Login -> LoginView(uiState, onTriggerEvent)
                Screens.Navigation -> NavigationView(uiState, onTriggerEvent)
                Screens.Admin -> AdminHomeView(onTriggerEvent)
                Screens.AdminMqtt -> AdminMqttView(uiState, onTriggerEvent)
                Screens.AdminItemManagement -> AdminItemManagementView(uiState, onTriggerEvent)
                Screens.Hub -> RoleDashboard("Hub", "Coordinate incoming orders and route requests.", uiState, onTriggerEvent)
                Screens.PosTestLab -> PosTestLabView(uiState, onTriggerEvent)
                Screens.Kitchen -> KitchenCounterBoard(uiState = uiState, onLogout = logout)
                Screens.Owner -> OwnerOrdersBoard(uiState = uiState, onLogout = logout)
                Screens.SweetSnackCounter -> CounterOrdersBoard(
                    title = "Sweet & Snack Counter",
                    description = "Track dessert and snack orders.",
                    station = CounterStation.SWEET_SNACK,
                    uiState = uiState,
                    onEditOrder = { orderId -> onTriggerEvent(UiEvents.OrderEvents(OrderUiEvents.Edit(orderId, null))) },
                    onAddOrder = openSnackOrder,
                    onLogout = logout
                )
                Screens.MainCourseCounter -> CounterOrdersBoard(
                    title = "Main Course Counter",
                    description = "Manage main course preparation queue.",
                    station = CounterStation.MAIN_COURSE,
                    uiState = uiState,
                    onEditOrder = { orderId -> onTriggerEvent(UiEvents.OrderEvents(OrderUiEvents.Edit(orderId, null))) },
                    onAddOrder = openMainCourseOrder,
                    onLogout = logout
                )
                Screens.CashCounter -> CashCounterBoard(
                    uiState = uiState,
                    onLogout = logout
                )
                Screens.SweetSnackAddEditOrder -> AddEditOrderView(
                    title = "Sweet & Snack Order",
                    uiState = uiState,
                    onBack = {},
                    onTriggerEvent = onTriggerEvent
                )
                Screens.MainCourseAddEditOrder -> AddEditOrderView(
                    title = "Main Course Order",
                    uiState = uiState,
                    onBack = {},
                    onTriggerEvent = onTriggerEvent
                )
            }

            if (uiState.isLoading) {
                Box(
                    modifier = Modifier.fillMaxSize(),
                    contentAlignment = Alignment.Center
                ) {
                    CircularProgressIndicator(color = MaterialTheme.colorScheme.onPrimary)
                }
            }
        }
    }
}
