@OptIn(ExperimentalMaterial3Api::class, ExperimentalFoundationApi::class)
@Composable
fun AddEditOrderView(
    title: String,
    uiState: UiState,
    onBack: () -> Unit,
    onTriggerEvent: (UiEvents) -> Unit
) {
    val _unusedTitle = title
    val categories = uiState.foodCategories
    val isPortrait = uiState.isPortrait
    var selectedCategory by remember(categories) { mutableStateOf(categories.firstOrNull()?.id) }
    val orders = OrderStore.orders
    val activeOrderId = uiState.activeOrderId
    val activeOrder = orders.firstOrNull { it.id == activeOrderId }
    var activeVariants by remember { mutableStateOf<List<FoodItem>>(emptyList()) }
    var activeVariant by remember { mutableStateOf<FoodItem?>(null) }
    var sheetQuantity by remember { mutableStateOf(1.0) }
    var editingLineId by remember { mutableStateOf<String?>(null) }
    val itemSheetState = rememberModalBottomSheetState(skipPartiallyExpanded = true)
    val optionsSheetState = rememberModalBottomSheetState(skipPartiallyExpanded = true)
    val customerSheetState = rememberModalBottomSheetState(skipPartiallyExpanded = true)
    var showOptionsSheet by remember { mutableStateOf(false) }
    var showCustomerSheet by remember { mutableStateOf(false) }
    val scope = rememberCoroutineScope()

    if (activeOrder == null) {
        onTriggerEvent(UiEvents.OrderEvents(OrderUiEvents.ClearActive))
        val fallback = uiState.activeOrderReturn ?: Screens.SweetSnackCounter
        onTriggerEvent(UiEvents.NavigationEvents(NavigationUiEvents.Show(fallback)))
        return
    }

    fun navigateBack() {
        val fallback = when (activeOrder.counter) {
            CounterStation.SWEET_SNACK -> Screens.SweetSnackCounter
            CounterStation.MAIN_COURSE -> Screens.MainCourseCounter
        }
        val target = uiState.activeOrderReturn ?: fallback
        onTriggerEvent(UiEvents.OrderEvents(OrderUiEvents.ClearActive))
        onTriggerEvent(UiEvents.NavigationEvents(NavigationUiEvents.Show(target)))
    }

    val orderType = activeOrder.orderType
    val storeMode = if (activeOrder.parcel || orderType == OrderType.ONLINE) StoreMode.PARCEL else StoreMode.DINE_IN
    val onlinePlatform = activeOrder.platform ?: OnlinePlatform.SWIGGY
    val orderLines = activeOrder.items
    val orderTotals = activeOrder.totals()
    var customerNameInput by remember(activeOrder.id) { mutableStateOf(activeOrder.customerName.orEmpty()) }
    var customerMobileInput by remember(activeOrder.id) { mutableStateOf(activeOrder.customerMobile.orEmpty()) }

    LaunchedEffect(showCustomerSheet, activeOrder.id) {
        if (showCustomerSheet) {
            customerNameInput = activeOrder.customerName.orEmpty()
            customerMobileInput = activeOrder.customerMobile.orEmpty()
        }
    }

    fun setActiveVariant(item: FoodItem, quantityOverride: Double? = null) {
        val minQty = if (orderType == OrderType.STORE) item.minStoreQuantity else item.minOnlineQuantity
        sheetQuantity = quantityOverride?.let { max(minQty, it) } ?: max(minQty, item.quantityStep)
        activeVariant = item
    }

    fun openVariantSheet(variants: List<FoodItem>, editingLine: DemoOrderLine? = null) {
        activeVariants = variants
        if (editingLine != null) {
            editingLineId = editingLine.lineId
            val variant = variants.firstOrNull { it.id == editingLine.itemId } ?: variants.first()
            setActiveVariant(variant, editingLine.quantity)
        } else {
            editingLineId = null
            setActiveVariant(variants.first())
        }
        scope.launch { itemSheetState.show() }
    }

    fun addOrUpdateLine(item: FoodItem, quantity: Double) {
        val orderId = activeOrder?.id ?: return
        OrderStore.update(orderId) { order ->
            val targetLineId = editingLineId
            val updatedLine = toOrderLine(item, quantity, targetLineId)
            val items = order.items.toMutableList()
            if (targetLineId != null) {
                val targetIndex = items.indexOfFirst { it.lineId == targetLineId }
                val duplicateIndex = items.indexOfFirst { it.itemId == updatedLine.itemId && it.variantName == updatedLine.variantName && it.lineId != targetLineId }
                when {
                    duplicateIndex >= 0 && targetIndex >= 0 -> {
                        items.removeAt(targetIndex)
                        items[duplicateIndex] = updatedLine.copy(lineId = items[duplicateIndex].lineId)
                    }
                    targetIndex >= 0 -> items[targetIndex] = updatedLine
                    else -> items.add(updatedLine)
                }
            } else {
                val existingIndex = items.indexOfFirst { it.itemId == updatedLine.itemId && it.variantName == updatedLine.variantName }
                if (existingIndex >= 0) {
                    items[existingIndex] = updatedLine.copy(lineId = items[existingIndex].lineId)
                } else {
                    items.add(updatedLine)
                }
            }
            editingLineId = null
            order.copy(items = items)
        }
    }

    fun removeLine(lineId: String) {
        val orderId = activeOrder?.id ?: return
        OrderStore.update(orderId) { order ->
            order.copy(items = order.items.filterNot { it.lineId == lineId })
        }
    }

    fun updateOrderTypeSelection(newType: OrderType) {
        OrderStore.update(activeOrder.id) { order ->
            val normalizedParcel = when (newType) {
                OrderType.ONLINE -> true
                OrderType.STORE -> if (order.orderType == OrderType.ONLINE) false else order.parcel
            }
            val partner = if (newType == OrderType.STORE) "" else order.partnerCode
            val platform = if (newType == OrderType.STORE) null else order.platform ?: OnlinePlatform.SWIGGY
            order.copy(orderType = newType, parcel = normalizedParcel, partnerCode = partner, platform = platform)
        }
    }

    fun updatePartnerCode(raw: String) {
        val sanitized = raw.filter { it.isDigit() }.take(4)
        OrderStore.update(activeOrder.id) { order -> order.copy(partnerCode = sanitized) }
    }

    fun updateStoreMode(mode: StoreMode) {
        OrderStore.update(activeOrder.id) { order ->
            val manualCharge = if (mode == StoreMode.DINE_IN) 0.0 else order.manualParcelCharge
            order.copy(parcel = mode == StoreMode.PARCEL || order.orderType == OrderType.ONLINE, manualParcelCharge = manualCharge)
        }
    }

    fun updateParcelCharge(value: Double) {
        OrderStore.update(activeOrder.id) { order -> order.copy(manualParcelCharge = roundTwo(value)) }
    }

    fun updateOnlinePlatform(platform: OnlinePlatform) {
        OrderStore.update(activeOrder.id) { order -> order.copy(platform = platform) }
    }

    if (activeVariant != null) {
        ModalBottomSheet(
            onDismissRequest = {
                scope.launch { itemSheetState.hide() }.invokeOnCompletion {
                    activeVariant = null
                    activeVariants = emptyList()
                    editingLineId = null
                }
            },
            sheetState = itemSheetState
        ) {
            val variants = activeVariants
            val item = activeVariant!!
            val minQty = if (orderType == OrderType.STORE) item.minStoreQuantity else item.minOnlineQuantity
            val step = max(0.1, item.quantityStep)
            Column(modifier = Modifier.padding(24.dp)) {
                Text(item.name, style = MaterialTheme.typography.titleLarge)
                if (variants.size > 1) {
                    Spacer(modifier = Modifier.height(12.dp))
                    Text("Variants", style = MaterialTheme.typography.titleMedium)
                    Spacer(modifier = Modifier.height(8.dp))
                    variants.forEach { option ->
                        Row(verticalAlignment = Alignment.CenterVertically) {
                            RadioButton(
                                selected = option.id == item.id,
                                onClick = { setActiveVariant(option, sheetQuantity) }
                            )
                            Column(modifier = Modifier.padding(start = 8.dp)) {
                                Text(option.variantName ?: "Standard", fontWeight = FontWeight.SemiBold)
                                Text("${option.priceFor(orderType).formatAmount()}", style = MaterialTheme.typography.bodySmall)
                            }
                        }
                    }
                } else {
                    item.variantName?.let { Text("Variant: $it", style = MaterialTheme.typography.bodyMedium) }
                }
                Spacer(modifier = Modifier.height(12.dp))
                Row(verticalAlignment = Alignment.CenterVertically, horizontalArrangement = Arrangement.SpaceBetween, modifier = Modifier.fillMaxWidth()) {
                    Text("Quantity")
                    Row(verticalAlignment = Alignment.CenterVertically) {
                        TextButton(onClick = {
                            sheetQuantity = max(minQty, sheetQuantity - step)
                            sheetQuantity = (sheetQuantity * 100).roundToInt() / 100.0
                        }) { Text("-") }
                        Text(sheetQuantity.asQuantityLabel(item.unitLabel), modifier = Modifier.padding(horizontal = 8.dp))
                        TextButton(onClick = {
                            sheetQuantity = sheetQuantity + step
                            sheetQuantity = (sheetQuantity * 100).roundToInt() / 100.0
                        }) { Text("+") }
                    }
                }
                Spacer(modifier = Modifier.height(16.dp))
                Button(onClick = {
                    addOrUpdateLine(item, sheetQuantity)
                    scope.launch { itemSheetState.hide() }.invokeOnCompletion {
                        activeVariant = null
                        activeVariants = emptyList()
                    }
                }, modifier = Modifier.fillMaxWidth()) {
                    Text("Add to Order")
                }
            }
        }
    }
    if (showCustomerSheet) {
        ModalBottomSheet(
            onDismissRequest = {
                scope.launch { customerSheetState.hide() }.invokeOnCompletion { showCustomerSheet = false }
            },
            sheetState = customerSheetState
        ) {
            CustomerDetailsSheet(
                name = customerNameInput,
                mobile = customerMobileInput,
                onNameChange = { customerNameInput = it },
                onMobileChange = { customerMobileInput = it },
                onDone = {
                    OrderStore.update(activeOrder.id) { order ->
                        order.copy(
                            customerName = customerNameInput.trim().ifBlank { null },
                            customerMobile = customerMobileInput.trim().ifBlank { null }
                        )
                    }
                    scope.launch { customerSheetState.hide() }.invokeOnCompletion { showCustomerSheet = false }
                }
            )
        }
    }
    if (showOptionsSheet) {
        ModalBottomSheet(
            onDismissRequest = {
                scope.launch { optionsSheetState.hide() }.invokeOnCompletion { showOptionsSheet = false }
            },
            sheetState = optionsSheetState
        ) {
            if (orderType == OrderType.STORE) {
                StoreOptionsSheet(
                    storeMode = storeMode,
                    parcelCharge = activeOrder.manualParcelCharge,
                    onModeChange = { mode -> updateStoreMode(mode) },
                    onParcelChange = { updateParcelCharge(it) },
                    onClose = {
                        scope.launch { optionsSheetState.hide() }.invokeOnCompletion { showOptionsSheet = false }
                    }
                )
            } else {
                OnlineOptionsSheet(
                    platform = onlinePlatform,
                    onPlatformChange = { updateOnlinePlatform(it) },
                    onClose = {
                        scope.launch { optionsSheetState.hide() }.invokeOnCompletion { showOptionsSheet = false }
                    }
                )
            }
        }
    }

    val containerModifier = Modifier
        .fillMaxSize()
        .background(MaterialTheme.colorScheme.background)
        .padding(16.dp)

    if (isPortrait) {
        Column(containerModifier) {
            OrderHeader(
                tokenNumber = activeOrder.tokenNumber,
                orderType = orderType,
                partnerOrderId = activeOrder.partnerCode.orEmpty(),
                onPartnerOrderIdChange = { updatePartnerCode(it) },
                onOrderTypeChange = { updateOrderTypeSelection(it) },
                onBack = { navigateBack() },
                onShowCustomer = {
                    customerNameInput = activeOrder.customerName.orEmpty()
                    customerMobileInput = activeOrder.customerMobile.orEmpty()
                    showCustomerSheet = true
                    scope.launch { customerSheetState.show() }
                },
                hasCustomerDetails = !activeOrder.customerName.isNullOrBlank() || !activeOrder.customerMobile.isNullOrBlank()
            )
            Spacer(modifier = Modifier.height(16.dp))
            CategoryCarousel(categories, selectedCategory ?: -1) { selectedCategory = it }
        Spacer(modifier = Modifier.height(12.dp))
        Box(modifier = Modifier.weight(1f)) {
            ItemGrid(categories, selectedCategory, Modifier.fillMaxSize(), true) { openVariantSheet(it) }
        }
        Spacer(modifier = Modifier.height(16.dp))
        OrderSummary(
            order = activeOrder,
            orderLines = orderLines,
            totals = orderTotals,
            storeMode = storeMode,
            onlinePlatform = activeOrder.platform,
            onRemove = ::removeLine,
            onEdit = { line ->
                val variants = categories.firstNotNullOfOrNull { category ->
                    category.items.filter { it.name == line.itemName }.takeIf { it.isNotEmpty() }
                } ?: emptyList()
                if (variants.isNotEmpty()) {
                    openVariantSheet(variants, line)
                }
            },
            onShowOptions = {
                showOptionsSheet = true
                scope.launch { optionsSheetState.show() }
            },
            onComplete = { navigateBack() },
            modifier = Modifier
                .fillMaxWidth()
                .weight(1f),
            expandLines = true
        )
        }
    } else {
        Row(containerModifier) {
            Column(modifier = Modifier.width(260.dp).fillMaxHeight()) {
                OrderHeader(
                    tokenNumber = activeOrder.tokenNumber,
                    orderType = orderType,
                    partnerOrderId = activeOrder.partnerCode.orEmpty(),
                    onPartnerOrderIdChange = { updatePartnerCode(it) },
                    onOrderTypeChange = { updateOrderTypeSelection(it) },
                    onBack = { navigateBack() },
                    onShowCustomer = {
                        customerNameInput = activeOrder.customerName.orEmpty()
                        customerMobileInput = activeOrder.customerMobile.orEmpty()
                        showCustomerSheet = true
                        scope.launch { customerSheetState.show() }
                    },
                    hasCustomerDetails = !activeOrder.customerName.isNullOrBlank() || !activeOrder.customerMobile.isNullOrBlank()
                )
                Spacer(modifier = Modifier.height(12.dp))
                LazyColumn(verticalArrangement = Arrangement.spacedBy(12.dp)) {
                    items(categories, key = { it.id }) { category ->
                        val selected = category.id == selectedCategory
                        Card(
                            modifier = Modifier
                                .fillMaxWidth()
                                .clickable { selectedCategory = category.id },
                            colors = CardDefaults.cardColors(
                                containerColor = if (selected) primary.copy(alpha = 0.1f) else MaterialTheme.colorScheme.surface
                            ),
                            border = if (selected) BorderStroke(1.dp, primary) else null
                        ) {
                            Column(modifier = Modifier.padding(12.dp)) {
                                Text(category.name, fontWeight = FontWeight.Bold)
                                Text("${category.items.size} items", style = MaterialTheme.typography.bodySmall)
                            }
                        }
                    }
                }
            }
            Spacer(modifier = Modifier.width(16.dp))
            Box(modifier = Modifier.weight(1f)) {
                ItemGrid(categories, selectedCategory, Modifier.fillMaxSize(), false) { openVariantSheet(it) }
            }
            Spacer(modifier = Modifier.width(16.dp))
            OrderSummary(
                order = activeOrder,
                orderLines = orderLines,
                totals = orderTotals,
                storeMode = storeMode,
                onlinePlatform = activeOrder.platform,
                onRemove = ::removeLine,
                onEdit = { line ->
                    val variants = categories.firstNotNullOfOrNull { category ->
                        val matching = category.items.filter { it.name == line.itemName }
                        matching.takeIf { it.isNotEmpty() }
                    } ?: emptyList()
                    if (variants.isNotEmpty()) {
                        openVariantSheet(variants, line)
                    }
                },
                onShowOptions = {
                    showOptionsSheet = true
                    scope.launch { optionsSheetState.show() }
                },
                onComplete = { navigateBack() },
                modifier = Modifier
                    .width(320.dp)
                    .fillMaxHeight()
            )
        }
    }
}

@Composable
private fun OrderHeader(
    tokenNumber: Int,
    orderType: OrderType,
    partnerOrderId: String,
    onPartnerOrderIdChange: (String) -> Unit,
    onOrderTypeChange: (OrderType) -> Unit,
    onBack: () -> Unit,
    onShowCustomer: () -> Unit,
    hasCustomerDetails: Boolean
) {
    Column(modifier = Modifier.fillMaxWidth()) {
        Box(modifier = Modifier.fillMaxWidth()) {
            Text(
                text = "Token #$tokenNumber",
                color = MaterialTheme.colorScheme.error,
                fontWeight = FontWeight.Bold,
                modifier = Modifier.align(Alignment.Center)
            )
            TextButton(onClick = onBack, modifier = Modifier.align(Alignment.CenterEnd)) { Text("Close") }
        }
        Spacer(modifier = Modifier.height(8.dp))
        Row(verticalAlignment = Alignment.CenterVertically, horizontalArrangement = Arrangement.spacedBy(16.dp)) {
            val toggleColor = MaterialTheme.colorScheme.error
            Row(verticalAlignment = Alignment.CenterVertically) {
                RadioButton(selected = orderType == OrderType.STORE, onClick = { onOrderTypeChange(OrderType.STORE) })
                Text("Store", color = toggleColor)
            }
            Row(verticalAlignment = Alignment.CenterVertically) {
                RadioButton(selected = orderType == OrderType.ONLINE, onClick = { onOrderTypeChange(OrderType.ONLINE) })
                Text("Online", color = toggleColor)
            }
        }
        if (orderType == OrderType.ONLINE) {
            Spacer(modifier = Modifier.height(8.dp))
            OutlinedTextField(
                value = partnerOrderId,
                onValueChange = onPartnerOrderIdChange,
                label = { Text("Partner code (4 digits)") },
                modifier = Modifier.fillMaxWidth()
            )
        }
        Spacer(modifier = Modifier.height(8.dp))
        TextButton(onClick = onShowCustomer) {
            Text(if (hasCustomerDetails) "Edit customer details" else "Add Customer details")
        }
    }
}

@Composable
private fun CustomerDetailsSheet(
    name: String,
    mobile: String,
    onNameChange: (String) -> Unit,
    onMobileChange: (String) -> Unit,
    onDone: () -> Unit
) {
    Column(modifier = Modifier.padding(24.dp)) {
        Text("Customer details", style = MaterialTheme.typography.titleLarge)
        Spacer(modifier = Modifier.height(12.dp))
        OutlinedTextField(
            value = name,
            onValueChange = onNameChange,
            label = { Text("Customer name (optional)") },
            modifier = Modifier.fillMaxWidth()
        )
        Spacer(modifier = Modifier.height(12.dp))
        OutlinedTextField(
            value = mobile,
            onValueChange = onMobileChange,
            label = { Text("Mobile (optional)") },
            modifier = Modifier.fillMaxWidth()
        )
        Spacer(modifier = Modifier.height(16.dp))
        Button(onClick = onDone, modifier = Modifier.fillMaxWidth()) {
            Text("Save")
        }
    }
}

@Composable
private fun CategoryCarousel(categories: List<FoodCategory>, selectedId: Int, onSelect: (Int) -> Unit) {
    val textColor = MaterialTheme.colorScheme.onBackground
    LazyRow(horizontalArrangement = Arrangement.spacedBy(8.dp)) {
        items(categories, key = { it.id }) { category ->
            val selected = category.id == selectedId
            Text(
                text = category.name,
                modifier = Modifier
                    .background(if (selected) primary.copy(alpha = 0.2f) else Color.LightGray.copy(alpha = 0.3f), MaterialTheme.shapes.medium)
                    .padding(horizontal = 16.dp, vertical = 8.dp)
                    .clickable { onSelect(category.id) },
                color = if (selected) primary else textColor
            )
        }
    }
}

@Composable
private fun StoreOptionsSheet(
    storeMode: StoreMode,
    parcelCharge: Double,
    onModeChange: (StoreMode) -> Unit,
    onParcelChange: (Double) -> Unit,
    onClose: () -> Unit
) {
    Column(modifier = Modifier.padding(24.dp)) {
        Text("Store order options", style = MaterialTheme.typography.titleLarge)
        Spacer(modifier = Modifier.height(16.dp))
        Row(verticalAlignment = Alignment.CenterVertically) {
            RadioButton(
                selected = storeMode == StoreMode.DINE_IN,
                onClick = { onModeChange(StoreMode.DINE_IN) }
            )
            Text("Dine-in", modifier = Modifier.padding(start = 8.dp))
        }
        Row(verticalAlignment = Alignment.CenterVertically) {
            RadioButton(
                selected = storeMode == StoreMode.PARCEL,
                onClick = { onModeChange(StoreMode.PARCEL) }
            )
            Text("Parcel", modifier = Modifier.padding(start = 8.dp))
        }
        if (storeMode == StoreMode.PARCEL) {
            Spacer(modifier = Modifier.height(12.dp))
            OutlinedTextField(
                value = parcelCharge.formatAmount(),
                onValueChange = { it.toDoubleOrNull()?.let(onParcelChange) },
                label = { Text("Parcel charge") },
                modifier = Modifier.fillMaxWidth()
            )
        }
        Spacer(modifier = Modifier.height(24.dp))
        Button(onClick = onClose, modifier = Modifier.fillMaxWidth()) {
            Text("Done")
        }
    }
}

@Composable
private fun OnlineOptionsSheet(
    platform: OnlinePlatform,
    onPlatformChange: (OnlinePlatform) -> Unit,
    onClose: () -> Unit
) {
    Column(modifier = Modifier.padding(24.dp)) {
        Text("Online platform", style = MaterialTheme.typography.titleLarge)
        Spacer(modifier = Modifier.height(16.dp))
        OnlinePlatform.values().forEach { option ->
            Row(verticalAlignment = Alignment.CenterVertically) {
                RadioButton(
                    selected = platform == option,
                    onClick = { onPlatformChange(option) }
                )
                Text(option.display(), modifier = Modifier.padding(start = 8.dp))
            }
        }
        Spacer(modifier = Modifier.height(24.dp))
        Button(onClick = onClose, modifier = Modifier.fillMaxWidth()) {
            Text("Done")
        }
    }
}

@OptIn(ExperimentalFoundationApi::class)
@Composable
private fun ItemGrid(
    categories: List<FoodCategory>,
    selectedCategoryId: Int?,
    modifier: Modifier,
    isCompact: Boolean,
    onItemSelected: (List<FoodItem>) -> Unit
) {
    val category = categories.firstOrNull { it.id == selectedCategoryId } ?: categories.firstOrNull()
    if (category == null) {
        Box(modifier = modifier.fillMaxSize(), contentAlignment = Alignment.Center) {
            Text("No items")
        }
        return
    }
    val grouped = category.items.sortedBy { it.name }.groupBy { it.name }
    val columns = if (isCompact) 2 else 0
    val cardHeight = if (isCompact) 92.dp else 120.dp
    LazyVerticalGrid(
        columns = if (columns > 0) GridCells.Fixed(columns) else GridCells.Adaptive(160.dp),
        modifier = modifier.fillMaxSize(),
        verticalArrangement = Arrangement.spacedBy(12.dp),
        horizontalArrangement = Arrangement.spacedBy(12.dp)
    ) {
        items(grouped.entries.toList(), key = { it.key }) { (name, variants) ->
            Card(
                modifier = Modifier
                    .fillMaxWidth()
                    .height(cardHeight)
                    .clickable { onItemSelected(variants) },
                colors = CardDefaults.cardColors(containerColor = MaterialTheme.colorScheme.surface)
            ) {
                val textAlign = if (isCompact) TextAlign.Center else TextAlign.Start
                val horizontalAlignment = if (isCompact) Alignment.CenterHorizontally else Alignment.Start
                val verticalArrangement = if (isCompact) Arrangement.Center else Arrangement.Top
                Column(
                    modifier = Modifier
                        .fillMaxSize()
                        .padding(12.dp),
                    horizontalAlignment = horizontalAlignment,
                    verticalArrangement = verticalArrangement
                ) {
                    Text(
                        name,
                        fontWeight = FontWeight.Bold,
                        maxLines = 2,
                        color = MaterialTheme.colorScheme.onSurface,
                        textAlign = textAlign
                    )
                    if (variants.size > 1) {
                        Text(
                            "${variants.size} variants",
                            style = MaterialTheme.typography.bodySmall,
                            color = MaterialTheme.colorScheme.onSurfaceVariant,
                            textAlign = textAlign
                        )
                    } else {
                        variants.firstOrNull()?.variantName?.let {
                            Text(
                                it,
                                style = MaterialTheme.typography.bodySmall,
                                color = MaterialTheme.colorScheme.onSurfaceVariant,
                                textAlign = textAlign
                            )
                        }
                    }
                }
            }
        }
    }
}

@Composable
private fun OrderSummary(
    order: DemoOrder,
    orderLines: List<DemoOrderLine>,
    totals: DemoOrderTotals,
    storeMode: StoreMode,
    onlinePlatform: OnlinePlatform?,
    onRemove: (String) -> Unit,
    onEdit: (DemoOrderLine) -> Unit,
    onShowOptions: () -> Unit,
    onComplete: () -> Unit = {},
    modifier: Modifier = Modifier,
    expandLines: Boolean = true
) {
    Column(
        modifier = modifier
            .border(1.dp, Color.LightGray, MaterialTheme.shapes.medium)
            .padding(16.dp)
    ) {
        Row(
            verticalAlignment = Alignment.CenterVertically,
            horizontalArrangement = Arrangement.SpaceBetween,
            modifier = Modifier.fillMaxWidth()
        ) {
            Text("Current Order", style = MaterialTheme.typography.titleMedium, color = MaterialTheme.colorScheme.onBackground)
            TextButton(onClick = onShowOptions) {
                Text("Order Options")
            }
        }
        val detailText = when (order.orderType) {
            OrderType.STORE -> {
                val parcelSuffix = if (order.parcel) {
                    val charge = totals.parcelCharge.formatAmount()
                    " (Parcel charge $charge)"
                } else ""
                "Mode: ${storeMode.display()}$parcelSuffix"
            }
            OrderType.ONLINE -> "Platform: ${(onlinePlatform ?: OnlinePlatform.SWIGGY).display()}"
        }
        Text(detailText, style = MaterialTheme.typography.bodySmall, color = MaterialTheme.colorScheme.onSurfaceVariant)
        Spacer(modifier = Modifier.height(12.dp))
        val listModifier = if (expandLines) Modifier.weight(1f) else Modifier.wrapContentHeight()
        if (orderLines.isEmpty()) {
            Box(modifier = listModifier.fillMaxWidth(), contentAlignment = Alignment.Center) {
                Text("No items yet", color = MaterialTheme.colorScheme.onSurfaceVariant)
            }
        } else {
            LazyColumn(modifier = listModifier, verticalArrangement = Arrangement.spacedBy(12.dp)) {
                items(orderLines, key = { it.lineId }) { line ->
                    Card {
                        Column(modifier = Modifier.padding(12.dp)) {
                            Row(horizontalArrangement = Arrangement.SpaceBetween, modifier = Modifier.fillMaxWidth()) {
                                Column {
                                    Text(line.itemName, fontWeight = FontWeight.Bold, color = MaterialTheme.colorScheme.onSurface)
                                    line.variantName?.let { Text(it, style = MaterialTheme.typography.bodySmall, color = MaterialTheme.colorScheme.onSurfaceVariant) }
                                }
                                Text(
                                    text = line.gross(order.orderType).formatMoney(),
                                    color = MaterialTheme.colorScheme.onSurface
                                )
                            }
                            Text("Qty: ${line.quantity} ${line.unitLabel}", color = MaterialTheme.colorScheme.onSurfaceVariant)
                            Row {
                                TextButton(onClick = { onEdit(line) }) { Text("Edit") }
                                TextButton(onClick = { onRemove(line.lineId) }) { Text("Remove") }
                            }
                        }
                    }
                }
            }
        }
        Spacer(modifier = Modifier.height(12.dp))
        Divider()
        Spacer(modifier = Modifier.height(12.dp))
        SummaryRow("Subtotal", totals.subtotalBeforeTax)
        if (totals.parcelCharge > 0) SummaryRow("Parcel", totals.parcelCharge)
        SummaryRow("SGST", totals.sgst)
        SummaryRow("CGST", totals.cgst)
        Divider(modifier = Modifier.padding(vertical = 8.dp))
        SummaryRow("Total", totals.total, bold = true)
        Spacer(modifier = Modifier.height(12.dp))
        Button(onClick = onComplete, modifier = Modifier.fillMaxWidth(), enabled = orderLines.isNotEmpty()) {
            Text("Save & Next")
        }
    }
}

@Composable
private fun SummaryRow(label: String, amount: Double, bold: Boolean = false) {
    Row(horizontalArrangement = Arrangement.SpaceBetween, modifier = Modifier.fillMaxWidth()) {
        Text(label, fontWeight = if (bold) FontWeight.Bold else FontWeight.Normal, color = MaterialTheme.colorScheme.onBackground)
        Text(
            text = "${amount.formatAmount()}",
            fontWeight = if (bold) FontWeight.Bold else FontWeight.Normal,
            color = MaterialTheme.colorScheme.onBackground
        )
    }
}

private fun StoreMode.display(): String = when (this) {
    StoreMode.DINE_IN -> "Dine-in"
    StoreMode.PARCEL -> "Parcel"
}

private fun OnlinePlatform.display(): String = when (this) {
    OnlinePlatform.SWIGGY -> "Swiggy"
    OnlinePlatform.ZOMATO -> "Zomato"
    OnlinePlatform.OTHER -> "Other"
}

private fun FoodItem.priceFor(orderType: OrderType): Double =
    if (orderType == OrderType.STORE) storePrice else onlinePrice

private fun Double.formatAmount(): String {
    val rounded = kotlin.math.round(this * 100) / 100.0
    val parts = rounded.toString().split('.')
    val decimals = if (parts.size > 1) parts[1].padEnd(2, '0').take(2) else "00"
    val whole = parts[0]
    return "$whole.$decimals"
}

private fun Double.asQuantityLabel(unit: String): String = "${formatAmount()} $unit"

@OptIn(ExperimentalUuidApi::class)
private fun toOrderLine(
    item: FoodItem,
    quantity: Double,
    existingLineId: String?
): DemoOrderLine {
    val roundedQty = roundTwo(quantity)
    val lineId = existingLineId ?: Uuid.random().toString()
    return DemoOrderLine(
        lineId = lineId,
        itemId = item.id,
        itemName = item.name,
        variantName = item.variantName,
        quantity = roundedQty,
        unitLabel = item.unitLabel,
        unitPriceStore = item.storePrice,
        unitPriceOnline = item.onlinePrice,
        gstRate = item.gstRate,
        source = item.source
    )
}
