package com.kalingas.pos.dev.presentation.dashboard.view

import androidx.compose.runtime.mutableStateListOf
import androidx.compose.runtime.snapshots.SnapshotStateList
import com.kalingas.pos.dev.presentation.order.viewmodel.OrderType
import com.kalingas.pos.dev.presentation.order.viewmodel.OnlinePlatform
import kotlin.math.round
import kotlin.uuid.ExperimentalUuidApi
import kotlin.uuid.Uuid

enum class CounterStation { SWEET_SNACK, MAIN_COURSE }

enum class DemoOrderStatus { OPEN, PREPARING, READY, BILLED, PAID }

enum class KitchenLineStatus { PENDING, IN_PROGRESS, FULFILLED, UNABLE }

enum class PaymentMethod { CASH, CARD, UPI }

data class DemoOrderLine(
    val lineId: String,
    val itemId: String,
    val itemName: String,
    val variantName: String? = null,
    val quantity: Double,
    val unitLabel: String,
    val unitPriceStore: Double,
    val unitPriceOnline: Double,
    val gstRate: Int,
    val source: String,
    val kitchenStatus: KitchenLineStatus? = null,
    val parcelCharge: Double = 0.0
) {
    fun unitPrice(orderType: OrderType): Double = if (orderType == OrderType.STORE) unitPriceStore else unitPriceOnline

    fun gross(orderType: OrderType): Double = roundMoney(unitPrice(orderType) * quantity)

    fun gstPortion(orderType: OrderType): Double {
        val gross = gross(orderType)
        return if (gstRate == 0) 0.0 else roundMoney(gross - gross / (1 + gstRate / 100.0))
    }
}

data class DemoOrder(
    val id: String,
    val tokenNumber: Int,
    val counter: CounterStation,
    val orderType: OrderType,
    val platform: OnlinePlatform? = null,
    val partnerCode: String? = null,
    val customerName: String? = null,
    val customerMobile: String? = null,
    val parcel: Boolean = false,
    val createdAt: String,
    val status: DemoOrderStatus,
    val items: List<DemoOrderLine>,
    val note: String? = null,
    val payment: DemoPayment? = null,
    val invoiceNumber: String? = null,
    val manualParcelCharge: Double = 0.0
) {
    fun totals(): DemoOrderTotals {
        val subtotalBeforeTax = roundMoney(items.sumOf { line ->
            val gross = line.gross(orderType)
            gross - line.gstPortion(orderType)
        })
        val gstTotal = roundMoney(items.sumOf { it.gstPortion(orderType) })
        val halfGst = roundMoney(gstTotal / 2)
        val parcelAuto = if (parcel || orderType == OrderType.ONLINE) roundMoney(items.sumOf { it.parcelCharge * it.quantity }) else 0.0
        val parcelManual = if (parcel || orderType == OrderType.ONLINE) roundMoney(manualParcelCharge) else 0.0
        val parcelCharge = roundMoney(parcelAuto + parcelManual)
        val total = roundMoney(subtotalBeforeTax + gstTotal + parcelCharge)
        return DemoOrderTotals(
            subtotalBeforeTax = subtotalBeforeTax,
            sgst = halfGst,
            cgst = halfGst,
            parcelCharge = parcelCharge,
            total = total
        )
    }
}

data class DemoPayment(
    val method: PaymentMethod,
    val amount: Double,
    val paidAt: String,
    val reference: String? = null
)

data class DemoOrderTotals(
    val subtotalBeforeTax: Double,
    val sgst: Double,
    val cgst: Double,
    val parcelCharge: Double,
    val total: Double
)

object OrderStore {
    private val _orders = mutableStateListOf<DemoOrder>()
    val orders: SnapshotStateList<DemoOrder> = _orders

    private var tokenCounter = 0

    fun ordersFor(counter: CounterStation): List<DemoOrder> = orders.filter { it.counter == counter }

    fun ordersNeedingKitchen(): List<DemoOrder> = orders.filter { order ->
        order.items.any { it.source == "KITCHEN" }
    }

    fun findOrder(orderId: String?): DemoOrder? = _orders.firstOrNull { it.id == orderId }

    fun createOrder(counter: CounterStation, orderType: OrderType = OrderType.STORE): DemoOrder {
        val token = nextToken(counter)
        val order = DemoOrder(
            id = newOrderId(),
            tokenNumber = token,
            counter = counter,
            orderType = orderType,
            parcel = orderType == OrderType.ONLINE,
            createdAt = timestamp(),
            status = DemoOrderStatus.OPEN,
            items = emptyList()
        )
        _orders.add(0, order)
        return order
    }

    fun markKitchenLine(orderId: String, lineId: String, status: KitchenLineStatus) {
        update(orderId) { order ->
            val updatedLines = order.items.map { line ->
                if (line.lineId == lineId) line.copy(kitchenStatus = status) else line
            }
            order.copy(
                items = updatedLines,
                status = resolveStatus(order.status, updatedLines)
            )
        }
    }

    @OptIn(ExperimentalUuidApi::class)
    fun markPaid(orderId: String, method: PaymentMethod, reference: String?) {
        val paidAt = timestamp()
        update(orderId) { order ->
            val totals = order.totals()
            order.copy(
                status = DemoOrderStatus.PAID,
                payment = DemoPayment(method, totals.total, paidAt, reference?.takeIf { it.isNotBlank() }),
                invoiceNumber = order.invoiceNumber ?: "INV-${order.tokenNumber}-${Uuid.random().toString().take(4).uppercase()}"
            )
        }
    }

    fun update(orderId: String, transform: (DemoOrder) -> DemoOrder) {
        val index = _orders.indexOfFirst { it.id == orderId }
        if (index >= 0) {
            _orders[index] = transform(_orders[index])
        }
    }

    private fun resolveStatus(current: DemoOrderStatus, lines: List<DemoOrderLine>): DemoOrderStatus {
        val kitchenLines = lines.filter { it.source == "KITCHEN" }
        if (kitchenLines.isEmpty()) return current
        val statuses = kitchenLines.map { it.kitchenStatus ?: KitchenLineStatus.PENDING }
        return when {
            statuses.all { it == KitchenLineStatus.FULFILLED } -> DemoOrderStatus.READY
            statuses.any { it == KitchenLineStatus.IN_PROGRESS } -> DemoOrderStatus.PREPARING
            statuses.any { it == KitchenLineStatus.UNABLE } -> DemoOrderStatus.BILLED
            else -> DemoOrderStatus.OPEN
        }
    }

    private var timestampSeed = 0
    private fun timestamp(): String {
        timestampSeed = (timestampSeed + 9) % (12 * 60)
        val totalMinutes = 9 * 60 + timestampSeed
        val hours = totalMinutes / 60
        val minutes = totalMinutes % 60
        val suffix = if (hours >= 12) "PM" else "AM"
        val hourDisplay = if (hours % 12 == 0) 12 else hours % 12
        val minuteDisplay = minutes.toString().padStart(2, '0')
        return "Today $hourDisplay:$minuteDisplay $suffix"
    }

    private fun nextToken(@Suppress("UNUSED_PARAMETER") counter: CounterStation): Int {
        tokenCounter += 1
        return tokenCounter
    }

    @OptIn(ExperimentalUuidApi::class)
    private fun newOrderId(): String = "ORD-${Uuid.random()}".uppercase()
}

fun Double.formatMoney(): String = "" + (roundMoney(this)).let {
    val parts = it.toString().split('.')
    val decimals = if (parts.size > 1) parts[1].padEnd(2, '0').take(2) else "00"
    parts[0] + "." + decimals
}

fun roundMoney(value: Double): Double = round(value * 100) / 100.0
