package com.kalingas.pos.dev.datasource.mqtt

import kotlinx.atomicfu.AtomicBoolean
import kotlinx.atomicfu.atomic

data class MQTTMessage(val topic: String, val payload: String)

object MQTTManager {

    private var platformInitializerInvoked = false
    private val connected: AtomicBoolean = atomic(false)

    var mqttCallback: (MQTTCallback) -> Unit = {}
    val messages: MutableList<MQTTMessage> = mutableListOf()

    fun onMQTTCallback(callback: (MQTTCallback) -> Unit) {
        mqttCallback = callback
    }

    fun ensurePlatformClient() {
        if (!platformInitializerInvoked) {
            platformInitializerInvoked = true
            initializePlatformMqttClient()
        }
    }

    fun createMqttClient() {
        ensurePlatformClient()
        mqttCallback(MQTTCallback.CreateMqttClient { })
    }

    fun connect() {
        ensurePlatformClient()
        messages.clear()
        if (!connected.value) {
            mqttCallback(MQTTCallback.Connect { })
        }
    }

    fun disconnect() {
        messages.clear()
        if (connected.value) {
            mqttCallback(MQTTCallback.Disconnect { })
        }
    }

    fun subscribe(topic: String) {
        mqttCallback(MQTTCallback.Subscribe(topic) { })
    }

    fun unsubscribe(topic: String) {
        mqttCallback(MQTTCallback.UnSubscribe(topic) { })
    }

    fun publish(topic: String, message: String) {
        mqttCallback(MQTTCallback.Publish(topic, message) { })
    }

    fun onTriggerMQTTEvent(event: MQTTEvents) {
        when (event) {
            MQTTEvents.OnConnected -> connected.value = true
            is MQTTEvents.OnDisconnected -> connected.value = false
            is MQTTEvents.OnMessageReceived -> messages.add(MQTTMessage(event.topic, event.message))
        }
    }
}

expect fun initializePlatformMqttClient()
