package com.kalingas.pos.dev.util

const val MQTT_SSL_PREFIX = "ssl://"
const val MQTT_TCP_PREFIX = "tcp://"
var MQTT_SSL = MQTT_SSL_PREFIX
var MQTT_COLON = ":"

private object MqttSettings {
    const val HOST = "broker.hivemq.com"
    const val TCP_PORT = "8883"
    const val WEB_PORT = "8084"
    const val USERNAME = ""
    const val PASSWORD = ""
    const val CERTIFICATE = ""
    const val LOCAL_BROKER_URL = "192.168.1.10"
    const val LOCAL_BROKER_PORT = "1883"
    const val LOCAL_BROKER_WEB_PORT = "8083"
}

fun getMQTTUrl(): String = MqttSettings.HOST

fun getMQTTPort(): String = MqttSettings.TCP_PORT

fun getMQTTWebPort(): String = MqttSettings.WEB_PORT

fun getMQTTUserName(): String = MqttSettings.USERNAME

fun getMQTTPassword(): String = MqttSettings.PASSWORD

fun getMQTTCertificate(): String = MqttSettings.CERTIFICATE

fun getLocalMqttUrl(): String = MqttSettings.LOCAL_BROKER_URL

fun getLocalMqttPort(): String = MqttSettings.LOCAL_BROKER_PORT

fun getLocalMqttWebPort(): String = MqttSettings.LOCAL_BROKER_WEB_PORT
