@Composable
fun AdminItemManagementView(uiState: UiState, onTriggerEvent: (UiEvents) -> Unit) {
    val sheetState = rememberModalBottomSheetState(skipPartiallyExpanded = true)
    val sheet = uiState.adminSheet

    Column(
        modifier = Modifier
            .fillMaxSize()
            .background(screenBackground)
            .padding(24.dp)
    ) {
        AdminSectionHeader(
            title = "Item Management",
            onBack = { onTriggerEvent(UiEvents.NavigationEvents(NavigationUiEvents.Show(Screens.Admin))) },
            onLogout = { onTriggerEvent(UiEvents.LoginEvents(LoginUiEvents.Logout)) }
        )
        Spacer(modifier = Modifier.height(8.dp))
        Row(modifier = Modifier.weight(1f)) {
            CategoryList(
            categories = uiState.foodCategories,
            modifier = Modifier
                .width(220.dp)
                .fillMaxHeight(),
                onTriggerEvent = onTriggerEvent
            )
            Spacer(modifier = Modifier.width(16.dp))
            SelectedCategoryItems(
                categories = uiState.foodCategories,
                onTriggerEvent = onTriggerEvent
            )
        }
        Spacer(modifier = Modifier.height(16.dp))
        Button(
            onClick = { onTriggerEvent(UiEvents.AdminEvents(AdminUiEvents.ShowCategorySheet(null))) },
            modifier = Modifier.fillMaxWidth()
        ) {
            Text("Add Food Category")
        }
    }

    if (sheet != null) {
        LaunchedEffect(sheet) { sheetState.show() }
        ModalBottomSheet(
            onDismissRequest = { onTriggerEvent(UiEvents.AdminEvents(AdminUiEvents.DismissSheet)) },
            sheetState = sheetState
        ) {
            when (sheet) {
                is AdminSheet.Category -> CategorySheet(sheet.form, onTriggerEvent)
                is AdminSheet.Item -> ItemSheet(sheet.form, onTriggerEvent)
            }
        }
    }
}

@Composable
private fun CategoryList(
    categories: List<FoodCategory>,
    modifier: Modifier,
    onTriggerEvent: (UiEvents) -> Unit
) {
    LazyColumn(
        modifier = modifier,
        verticalArrangement = Arrangement.spacedBy(12.dp)
    ) {
        items(categories, key = { it.id }) { category ->
            Card(
                modifier = Modifier
                    .fillMaxWidth()
                    .clickable { onTriggerEvent(UiEvents.AdminEvents(AdminUiEvents.ToggleCategory(category.id))) },
                shape = MaterialTheme.shapes.medium
            ) {
                Column(modifier = Modifier.padding(12.dp)) {
                    Row(
                        verticalAlignment = Alignment.CenterVertically,
                        modifier = Modifier.fillMaxWidth()
                    ) {
                        Column(modifier = Modifier.weight(1f)) {
                            Text(text = category.name, style = MaterialTheme.typography.titleMedium, color = primary)
                            Text(text = if (category.isActive) "Active" else "Hidden", style = MaterialTheme.typography.bodySmall)
                        }
                        Text(
                            text = "Edit",
                            color = primary,
                            modifier = Modifier.clickable {
                                onTriggerEvent(UiEvents.AdminEvents(AdminUiEvents.ShowCategorySheet(category.id)))
                            }
                        )
                    }
                    if (category.isExpanded) {
                        Spacer(modifier = Modifier.height(8.dp))
                        Text(text = "${category.items.size} items", style = MaterialTheme.typography.bodySmall)
                        Spacer(modifier = Modifier.height(8.dp))
                        Button(
                            onClick = { onTriggerEvent(UiEvents.AdminEvents(AdminUiEvents.ShowItemSheet(category.id, null))) },
                            modifier = Modifier.fillMaxWidth()
                        ) {
                            Text("Add Item")
                        }
                    }
                }
            }
        }
    }
}

@Composable
private fun SelectedCategoryItems(
    categories: List<FoodCategory>,
    onTriggerEvent: (UiEvents) -> Unit
) {
    val selected = categories.firstOrNull { it.isExpanded } ?: categories.firstOrNull()
    Column(modifier = Modifier.fillMaxSize()) {
        if (selected == null) {
            Box(modifier = Modifier.fillMaxSize(), contentAlignment = Alignment.Center) {
                Text("No categories available")
            }
            return
        }
        Text(text = selected.name, style = MaterialTheme.typography.titleLarge, color = primary)
        Spacer(modifier = Modifier.height(8.dp))
        val grouped = selected.items.groupBy { it.name }
            .map { entry -> entry.key to entry.value.sortedBy { it.variantName ?: it.name } }
            .sortedBy { it.first }
        LazyColumn(
            modifier = Modifier.fillMaxWidth(),
            verticalArrangement = Arrangement.spacedBy(12.dp)
        ) {
            items(grouped, key = { (_, variants) -> variants.joinToString { it.id } }) { (baseName, variants) ->
                Card {
                    Column(modifier = Modifier.padding(12.dp)) {
                        Text(baseName, style = MaterialTheme.typography.titleMedium, color = primary)
                        variants.forEachIndexed { index, variant ->
                            if (index > 0) {
                                Spacer(modifier = Modifier.height(8.dp))
                            }
                            VariantRow(
                                categoryId = selected.id,
                                item = variant,
                                onTriggerEvent = onTriggerEvent
                            )
                        }
                    }
                }
            }
        }
    }
}

@Composable
private fun VariantRow(categoryId: Int, item: FoodItem, onTriggerEvent: (UiEvents) -> Unit) {
    Row(verticalAlignment = Alignment.CenterVertically) {
        Column(modifier = Modifier.weight(1f)) {
            Text(
                text = item.variantName?.takeIf { it.isNotBlank() } ?: "Standard",
                style = MaterialTheme.typography.bodyMedium
            )
            Text(
                text = "Store ${formatPriceLabel(item.storePriceText, item.storePrice)} | Online ${formatPriceLabel(item.onlinePriceText, item.onlinePrice)}",
                style = MaterialTheme.typography.bodySmall
            )
            Text(
                text = "Unit ${item.unitLabel.uppercase()} | Step ${item.quantityStep.quantityString()} | Min store ${item.minStoreQuantity.quantityString()} | Min online ${item.minOnlineQuantity.quantityString()}",
                style = MaterialTheme.typography.bodySmall
            )
            Text("Source ${item.source} | GST ${item.gstRate}%", style = MaterialTheme.typography.bodySmall)
        }
        Column(horizontalAlignment = Alignment.End, modifier = Modifier.padding(start = 12.dp)) {
            Switch(
                checked = item.isActive,
                onCheckedChange = {
                    onTriggerEvent(
                        UiEvents.AdminEvents(AdminUiEvents.ToggleItemActive(categoryId, item.id))
                    )
                }
            )
            Text(
                text = "Edit",
                color = primary,
                modifier = Modifier
                    .padding(top = 4.dp)
                    .clickable {
                        onTriggerEvent(UiEvents.AdminEvents(AdminUiEvents.ShowItemSheet(categoryId, item.id)))
                    }
            )
        }
    }
}

@Composable
private fun CategorySheet(form: CategoryFormState, onTriggerEvent: (UiEvents) -> Unit) {
    Column(modifier = Modifier.padding(16.dp)) {
        Text(text = if (form.id == null) "Add Category" else "Edit Category", style = MaterialTheme.typography.titleLarge)
        Spacer(modifier = Modifier.height(16.dp))
        OutlinedTextField(
            value = form.name,
            onValueChange = { onTriggerEvent(UiEvents.AdminEvents(AdminUiEvents.UpdateCategoryName(it))) },
            label = { Text("Category name") },
            modifier = Modifier.fillMaxWidth(),
            isError = form.nameError
        )
        if (form.nameError) {
            Text("Name required", color = MaterialTheme.colorScheme.error, style = MaterialTheme.typography.bodySmall)
        }
        Row(
            verticalAlignment = Alignment.CenterVertically,
            modifier = Modifier.padding(top = 12.dp)
        ) {
            Text("Active")
            Switch(
                checked = form.isActive,
                onCheckedChange = { onTriggerEvent(UiEvents.AdminEvents(AdminUiEvents.UpdateCategoryActive(it))) }
            )
        }
        Spacer(modifier = Modifier.height(16.dp))
        Button(
            onClick = { onTriggerEvent(UiEvents.AdminEvents(AdminUiEvents.SaveCategoryForm)) },
            modifier = Modifier.fillMaxWidth()
        ) {
            Text("Save Category")
        }
    }
}

@Composable
private fun ItemSheet(form: ItemFormState, onTriggerEvent: (UiEvents) -> Unit) {
    val sourceOptions = listOf("COUNTER", "KITCHEN")
    val unitOptions = ItemUnit.entries
    val scrollState = rememberScrollState()

    Column(
        modifier = Modifier
            .padding(16.dp)
            .verticalScroll(scrollState)
    ) {
        val isEdit = form.originalName != null
        Text(text = if (isEdit) "Edit Item" else "Add Item", style = MaterialTheme.typography.titleLarge)
        if (form.categoryName.isNotBlank()) {
            Text(
                text = form.categoryName,
                style = MaterialTheme.typography.titleMedium.copy(fontWeight = FontWeight.SemiBold),
                modifier = Modifier.padding(top = 6.dp, bottom = 6.dp),
                color = MaterialTheme.colorScheme.primary
            )
        }
        Spacer(modifier = Modifier.height(6.dp))
        ItemField("Item name", form.name, form.nameError) {
            onTriggerEvent(UiEvents.AdminEvents(AdminUiEvents.UpdateItemField(ItemField.Name, it)))
        }
        form.variants.forEachIndexed { index, variant ->
            Spacer(modifier = Modifier.height(12.dp))
            VariantEditor(
                index = index,
                variant = variant,
                unitOptions = unitOptions,
                sourceOptions = sourceOptions,
                canRemove = form.variants.size > 1,
                onTriggerEvent = onTriggerEvent
            )
        }
        Spacer(modifier = Modifier.height(12.dp))
        Button(
            onClick = { onTriggerEvent(UiEvents.AdminEvents(AdminUiEvents.AddVariant)) },
            modifier = Modifier.fillMaxWidth()
        ) {
            Text("Add Variant")
        }
        Spacer(modifier = Modifier.height(16.dp))
        Button(
            onClick = { onTriggerEvent(UiEvents.AdminEvents(AdminUiEvents.SaveItemForm)) },
            modifier = Modifier.fillMaxWidth()
        ) {
            Text("Save Item")
        }
    }
}

@Composable
private fun VariantEditor(
    index: Int,
    variant: VariantFormState,
    unitOptions: List<ItemUnit>,
    sourceOptions: List<String>,
    canRemove: Boolean,
    onTriggerEvent: (UiEvents) -> Unit
) {
    var unitExpanded by remember(variant.id, index) { mutableStateOf(false) }
    var sourceExpanded by remember(variant.id, index) { mutableStateOf(false) }

    Column(
        modifier = Modifier
            .fillMaxWidth()
            .background(MaterialTheme.colorScheme.surfaceVariant.copy(alpha = 0.2f), MaterialTheme.shapes.medium)
            .padding(12.dp)
    ) {
        Row(
            verticalAlignment = Alignment.CenterVertically,
            horizontalArrangement = Arrangement.SpaceBetween,
            modifier = Modifier.fillMaxWidth()
        ) {
            Text("Variant ${index + 1}", style = MaterialTheme.typography.titleSmall, color = primary)
            if (canRemove) {
                Text(
                    text = "Remove",
                    color = MaterialTheme.colorScheme.error,
                    modifier = Modifier.clickable {
                        onTriggerEvent(UiEvents.AdminEvents(AdminUiEvents.RemoveVariant(index)))
                    }
                )
            }
        }
        ItemField("Variant name", variant.variantName, ItemField.VariantName in variant.errors) {
            onTriggerEvent(UiEvents.AdminEvents(AdminUiEvents.UpdateVariantField(index, ItemField.VariantName, it)))
        }
        Row(modifier = Modifier.fillMaxWidth(), horizontalArrangement = Arrangement.spacedBy(12.dp)) {
            Box(modifier = Modifier.weight(1f)) {
                ItemField("Store price", variant.storePrice, ItemField.StorePrice in variant.errors) {
                    onTriggerEvent(UiEvents.AdminEvents(AdminUiEvents.UpdateVariantField(index, ItemField.StorePrice, it)))
                }
            }
            Box(modifier = Modifier.weight(1f)) {
                ItemField("Online price", variant.onlinePrice, ItemField.OnlinePrice in variant.errors) {
                    onTriggerEvent(UiEvents.AdminEvents(AdminUiEvents.UpdateVariantField(index, ItemField.OnlinePrice, it)))
                }
            }
        }
        ItemField("GST %", variant.gstRate, ItemField.GstRate in variant.errors) {
            onTriggerEvent(UiEvents.AdminEvents(AdminUiEvents.UpdateVariantField(index, ItemField.GstRate, it)))
        }
        Box(
            modifier = Modifier
                .fillMaxWidth()
                .padding(top = 8.dp)
        ) {
            OutlinedTextField(
                value = variant.unit.displayLabel(),
                onValueChange = {},
                label = { Text("Unit type") },
                modifier = Modifier
                    .fillMaxWidth()
                    .pointerInput(Unit) {
                        detectTapGestures { unitExpanded = true }
                    },
                readOnly = true,
                trailingIcon = {
                    IconButton(onClick = { unitExpanded = true }) {
                        Icon(Icons.Filled.ArrowDropDown, contentDescription = "Select unit type")
                    }
                }
            )
            DropdownMenu(
                expanded = unitExpanded,
                onDismissRequest = { unitExpanded = false },
                modifier = Modifier.fillMaxWidth()
            ) {
                unitOptions.forEach { option ->
                    DropdownMenuItem(
                        text = { Text(option.displayLabel()) },
                        onClick = {
                            unitExpanded = false
                            onTriggerEvent(UiEvents.AdminEvents(AdminUiEvents.UpdateVariantUnit(index, option)))
                        }
                    )
                }
            }
        }
        ItemField("Unit label (kg/unit/etc.)", variant.unitLabel, ItemField.UnitLabel in variant.errors) {
            onTriggerEvent(UiEvents.AdminEvents(AdminUiEvents.UpdateVariantField(index, ItemField.UnitLabel, it)))
        }
        Row(modifier = Modifier.fillMaxWidth(), horizontalArrangement = Arrangement.spacedBy(12.dp)) {
            Box(modifier = Modifier.weight(1f)) {
                ItemField("Quantity step", variant.quantityStep, ItemField.QuantityStep in variant.errors) {
                    onTriggerEvent(UiEvents.AdminEvents(AdminUiEvents.UpdateVariantField(index, ItemField.QuantityStep, it)))
                }
            }
            Box(modifier = Modifier.weight(1f)) {
                ItemField("Min store qty", variant.minStoreQuantity, ItemField.MinStoreQuantity in variant.errors) {
                    onTriggerEvent(UiEvents.AdminEvents(AdminUiEvents.UpdateVariantField(index, ItemField.MinStoreQuantity, it)))
                }
            }
        }
        ItemField("Min online qty", variant.minOnlineQuantity, ItemField.MinOnlineQuantity in variant.errors) {
            onTriggerEvent(UiEvents.AdminEvents(AdminUiEvents.UpdateVariantField(index, ItemField.MinOnlineQuantity, it)))
        }
        Box(
            modifier = Modifier
                .fillMaxWidth()
                .padding(top = 8.dp)
        ) {
            OutlinedTextField(
                value = variant.source,
                onValueChange = {},
                label = { Text("Source (COUNTER/KITCHEN)") },
                modifier = Modifier
                    .fillMaxWidth(),
                readOnly = true,
                isError = ItemField.Source in variant.errors,
                trailingIcon = {
                    IconButton(onClick = { sourceExpanded = true }) {
                        Icon(Icons.Filled.ArrowDropDown, contentDescription = "Select source")
                    }
                }
            )
            DropdownMenu(
                expanded = sourceExpanded,
                onDismissRequest = { sourceExpanded = false },
                modifier = Modifier.fillMaxWidth()
            ) {
                sourceOptions.forEach { option ->
                    DropdownMenuItem(
                        text = { Text(option) },
                        onClick = {
                            sourceExpanded = false
                            onTriggerEvent(UiEvents.AdminEvents(AdminUiEvents.UpdateVariantField(index, ItemField.Source, option)))
                        }
                    )
                }
            }
        }
        Row(
            verticalAlignment = Alignment.CenterVertically,
            modifier = Modifier.padding(top = 12.dp)
        ) {
            Text("Active")
            Switch(
                checked = variant.isActive,
                onCheckedChange = { onTriggerEvent(UiEvents.AdminEvents(AdminUiEvents.UpdateVariantActive(index, it))) }
            )
        }
    }
}

@Composable
private fun ItemField(label: String, value: String, isError: Boolean, onValueChange: (String) -> Unit) {
    OutlinedTextField(
        value = value,
        onValueChange = onValueChange,
        label = { Text(label) },
        modifier = Modifier.fillMaxWidth(),
        isError = isError
    )
    if (isError) {
        Text("Invalid $label", color = MaterialTheme.colorScheme.error, style = MaterialTheme.typography.bodySmall)
    }
    Spacer(modifier = Modifier.height(8.dp))
}

private fun formatPriceLabel(raw: String, fallback: Double): String {
    if (raw.isBlank()) return fallback.currencyString()
    val trimmed = raw.trim()
    return if (trimmed.startsWith("")) trimmed else "$trimmed"
}

private fun Double.currencyString(): String {
    val longValue = toLong()
    return if (this == longValue.toDouble()) "$longValue" else "${round(this * 100.0) / 100.0}"
}

private fun Double.quantityString(): String {
    val longValue = toLong()
    return if (this == longValue.toDouble()) longValue.toString() else "${round(this * 100.0) / 100.0}"
}

private fun ItemUnit.displayLabel(): String {
    val pretty = name.lowercase().replace('_', ' ')
    return pretty.replaceFirstChar { ch ->
        if (ch.isLowerCase()) ch.titlecase() else ch.toString()
    }
}
